<template>
  <div v-if="hasitems">
    <slot></slot>
    <div class="flex justify-center my-4">
      <v-pagination
        class="text-center"
        v-model="page"
        :pages="pageCount"
        :range-size="1"
        active-color="#DCEDFF"
      />
    </div>
  </div>
  <p class="text-xl text-center py-4" v-else>No {{ title }} Data to show</p>
</template>

<script>
import VPagination from "vue3-pagination";
import "vue3-pagination/dist/vue3-pagination.css";

export default {
  props: {
    title: { type: String, required: true },
    items: { type: Array, required: true },
  },
  data() {
    return {
      page: 1,
      datainPage: 5,
    };
  },
  emits: ["get-paged-items"],
  components: { VPagination },
  created() {
    this.updateHandler();
  },
  watch: {
    pagedItems(newValue) {
      this.validatePagination();
      this.$emit("get-paged-items", newValue);
    },
  },
  computed: {
    pagedItems() {
      const pagedItems = this.items.slice(
        this.page * this.datainPage - this.datainPage,
        this.page * this.datainPage
      );
      return pagedItems;
    },

    pageCount() {
      const itemCount = this.items.length;
      return Math.ceil(itemCount / this.datainPage);
    },
    hasitems() {
      return this.items && this.items.length > 0;
    },
  },
  methods: {
    updateHandler() {
      this.$emit("get-paged-items", this.pagedItems);
    },
    validatePagination() {
      if (this.pageCount <= 1) {
        this.page = 1;
      }
    },
  },
};
</script>

<style scoped></style>
