export default {
  packages(state, getters, rootState, rootGetters) {
    let formattedPackages = [];

    const packages = state.packages;

    for (const thePackage of packages) {
      // find product by package (productId)
      const theProduct = rootGetters["products/getProductById"](
        thePackage.productId
      );
      if (theProduct) {
        formattedPackages.push({
          ...thePackage,
          product: theProduct.name,
        });
      }
    }

    return formattedPackages;
  },
  hasPackages(state) {
    return state.packages && state.packages.length > 0;
  },
  status(state) {
    return state.status;
  },
  // sGetPackagesById: (state) => (id) => {
  //   return state.packages.find((thePackage) => thePackage.id === id);
  // },
  getPackagesById: (state) => (id) => {
    console.log(state.packages);
    return state.packages.find((thePackage) => thePackage.id === id);
  },
  getPackagesByProductId: (state) => (productId) => {
    return state.packages.filter(
      (thePackage) => thePackage.productId === productId
    );
  },
  getPackagesByProductName: (state, getters) => (productName) => {
    const regex = new RegExp(`${productName}.*`, "i");
    const packages = getters.packages;
    const filteredOffers = packages.filter((thePackage) =>
      regex.test(thePackage.product)
    );
    return filteredOffers;
  },
  getPackagesByStatus: (state) => (status) => {
    return state.packages.filter((thePackage) => thePackage.status === status);
  },
  getPackagesByPriceMore: (state) => (price) => {
    return state.packages.filter(
      (thePackage) => parseFloat(thePackage.salePrice) >= parseFloat(price)
    );
  },
  getPackagesByPriceLess: (state) => (price) => {
    return state.packages.filter(
      (thePackage) => parseFloat(thePackage.salePrice) <= parseFloat(price)
    );
  },
  getPackagesByFilter: (state, getters) => (filterPayload) => {
    const filterType = filterPayload.type;
    const filterValue = filterPayload.value;

    if (!filterValue) {
      return getters.packages;
    }
    if (filterType === "product") {
      // all filterd Offer return array
      return getters.getPackagesByProductName(filterValue);
    } else if (filterType === "status") {
      return getters.getPackagesByStatus(filterValue);
    } else if (filterType === "priceMore") {
      return getters.getPackagesByPriceMore(filterValue);
    } else if (filterType === "priceLess") {
      return getters.getPackagesByPriceLess(filterValue);
    } else if (filterType === "offerId") {
      const offer = getters.getPackagesById(filterValue);
      if (!offer || offer.length === 0) {
        return null;
      } else {
        const offerArr = new Array(offer);
        return offerArr;
      }
    }
  },
};
