<template>
  <div v-if="logVisible">
    This is Offer main
    {{ hasPackages }}
    {{ hasShops }}
    {{ hasOffers }}
    {{ offers }}
  </div>

  <div v-if="isloading" class="flex justify-center items-center h-1/3">
    <base-spinner> </base-spinner>
  </div>
  <div v-else>
    <div v-if="hasProducts && hasPackages && hasShops && hasOffers">
      <router-view></router-view>
      <toast
        :show="!!error"
        fixed
        :message="error"
        mode="error"
        @close="closeError()"
      />
    </div>
    <div v-else class="flex justify-center items-center h-1/3">
      <base-spinner> </base-spinner>
    </div>
  </div>
</template>

<script>
import { isLogVisible } from "../../js/debug.js";
import LoadDataMixin from "../../mixins/loadData.js";

export default {
  mixins: [LoadDataMixin], // loadProduct(), loadPackage(), loadShop();
  data() {
    return {
      isloading: false,
      error: null,
      isDelete: false,
    };
  },

  created() {
    this.loadData();
  },
  computed: {
    logVisible() {
      return isLogVisible();
    },
    hasProducts() {
      return this.$store.getters["products/hasProducts"];
    },
    hasPackages() {
      return this.$store.getters["packages/hasPackages"];
    },
    hasShops() {
      return this.$store.getters["shops/hasShops"];
    },
    hasOffers() {
      return this.$store.getters["offers/hasOffers"];
    },
    offers() {
      return this.$store.getters["offers/offers"];
    },
  },

  methods: {
    async loadData() {
      this.isloading = true;
      await this.loadShop();
      console.log("loadShop");
      await this.loadProduct();
      console.log("loadProduct");
      await this.loadPackage();
      console.log("loadPackage");
      await this.loadOffer();
      this.isloading = false;
    },
    async loadOffer() {
      // try {
      await this.$store.dispatch("offers/setOffer");
      // } catch (e) {
      //   this.error = e.message;
      // }
    },
    closeError() {
      this.error = null;
    },
  },
};
</script>
