import { db } from "../../../js/firebaseDb.js";
import { isLogVisible } from "../../../js/debug.js";

export default {
  async updateTag(context, submittedData) {
    const id = submittedData.id;
    const newTag = {
      name: submittedData.tagName,
    };

    // === Data that need to be sent === //
    var tagRef = db.collection(`tags`).doc(id);

    // Set all field of the Quota
    await tagRef.update(newTag).catch((error) => {
      // The document probably doesn't exist.
      console.error("Error updating document: ", error);
    });

    // update data in vuex
    newTag.id = id;
    const tags = context.getters.tags;
    const selectedTag = tags.find((tag) => tag.id === id);
    var index = tags.indexOf(selectedTag);

    if (index !== -1) {
      tags.splice(index, 1, newTag);
      console.log("updated Tag : " + index);
    }

    context.commit("setTag", tags);
  },

  async addTag(context, submittedData) {
    const payload = {
      name: submittedData.tagName,
    };

    // Update data in Firebase
    const tagColl = db.collection(`tags`);
    const response = await tagColl.add(payload).catch((error) => {
      console.error("Error adding document: ", error);
    });
    if (response.empty) {
      const error = new Error("Can't send data to server");
      console.log(error);
      throw error;
    }

    payload.id = response.id;

    context.commit("addTag", {
      ...payload,
    });
  },

  async setTags(context) {
    const tagsColl = db.collection(`tags`);
    const tagsSnapshot = await tagsColl.get().catch((error) => {
      console.error("Error getting documents: ", error);
    });

    if (isLogVisible()) {
      console.log(tagsSnapshot);
    }

    if (tagsSnapshot.empty) {
      context.commit("clearOffer");
      const error = new Error("No such document!");
      console.log(error);
      throw error;
    }

    // console.log(responseData);

    const tags = [];
    await tagsSnapshot.forEach((doc) => {
      const newOffer = {
        id: doc.id,
        name: doc.data().name,
      };

      tags.push(newOffer);
    });
    context.commit("setTag", tags.reverse());
  },

  async deleteTag(context, payload) {
    const tagId = payload.tagId;
    const theTag = context.getters.getTagById(tagId);

    // update data in Firbase
    const subColl = db.collection(`tags`);
    subColl
      .doc(tagId)
      .delete()
      .catch((error) => {
        console.error("Error removing document: ", error);
      });

    // update data in VueX
    console.log("start deleteSubject in VueX" + index);
    const tags = context.state.tags;
    var index = tags.indexOf(theTag);
    if (index !== -1) {
      tags.splice(index, 1);
      console.log("removed" + index);
    }
    context.commit("setTag", tags);
  },
};
