<template>
  <div class="p-2">
    <div>
      <!-- Content go here -->
      <page-header title="Offers" subtitle="Offers History"></page-header>
      <div v-if="isloading" class="flex justify-center items-center h-1/3">
        <base-spinner> </base-spinner>
      </div>
      <div v-else>
        <base-card>
          <div class="grid grid-cols-5 gap-4">
            <div>
              <p>Product</p>
              <h3 class="text-primary-400 font-semibold text-xl">
                {{ offerDetail.product }}
              </h3>
            </div>
            <div>
              <p>Package</p>
              <h3 class="text-primary-400 font-semibold text-xl">
                {{ offerDetail.package }}
              </h3>
            </div>
            <div>
              <p>Platform</p>
              <h3 class="text-primary-400 font-semibold text-xl">
                {{ offerDetail.platform }}
              </h3>
            </div>
            <div>
              <p>Shop</p>
              <h3 class="text-primary-400 font-semibold text-xl">
                {{ offerDetail.shop }}
              </h3>
            </div>
            <div class="flex justify-center items-center">
              <base-button link mode="outline" :to="'/offers/' + id"
                ><span class="whitespace-nowrap">Offer Detail</span>
              </base-button>
            </div>
          </div>
        </base-card>
        <div class="mt-5">
          <table-pagination
            title="Prices"
            :items="pricesList"
            @get-paged-items="getPagedItems"
          >
            <table class="table-control">
              <thead>
                <tr>
                  <base-table-head
                    :head-items="['Time', 'Price', 'Currency', 'Stock']"
                  ></base-table-head>
                </tr>
              </thead>
              <tbody class="table-control">
                <prices-list-item
                  v-for="price in pagedPrices"
                  :key="price.id"
                  :dataList="price"
                ></prices-list-item>
              </tbody>
            </table>
          </table-pagination>
        </div>
      </div>
    </div>
  </div>
  {{ pricesList }}
</template>

<script>
import PricesListItem from "../../components/offers/PricesListItem.vue";
import TablePagination from "../../components/UI/TablePagination.vue";
import BaseTableHead from "../../components/UI/BaseTableHead.vue";

export default {
  props: ["id"],
  components: { PricesListItem, TablePagination, BaseTableHead },
  data() {
    return { pagedPrices: null, isloading: false };
  },
  created() {
    this.loadData();
  },
  computed: {
    offerDetail() {
      return this.$store.getters["offers/getOffersById"](this.id);
    },
    pricesList() {
      return this.$store.getters["offers/pricesList"];
    },
  },
  methods: {
    async loadData() {
      this.isloading = true;
      await this.setPricesList();
      this.isloading = false;
    },

    async setPricesList() {
      try {
        await this.$store.dispatch("offers/setPricesList", {
          plId: this.id,
        });
      } catch (e) {
        this.error = e.message;
      }
    },
    getPagedItems(pagedItems) {
      this.pagedPrices = pagedItems;
    },
  },
};
</script>

<style scoped></style>
