<template>
  <!-- This is Shops Page -->
  <div class="p-2">
    <div>
      <page-header title="Shops" subtitle="Shops List"></page-header>
      <base-card>
        <form @submit.prevent="null" class="flex items-center">
          <h4 class="flex-auto mr-4 font-bold">Search</h4>
          <select
            name="cars"
            id="cars"
            class="flex-auto mr-4 form-control"
            v-model="filterType"
          >
            <option value="shopId">Shop ID</option>
            <option value="shopName">Shop Name</option>
            <option value="currency">Currency</option>
            <option value="status">Status</option>
          </select>
          <input type="text" class="form-control  mr-4" v-model="filterValue" />
          <base-button>Search</base-button>
          <base-button link mode="flat" :to="{ name: 'shops-add' }"
            ><span class="whitespace-nowrap">+ Shops</span></base-button
          >
        </form>
      </base-card>
      <div class="mt-5">
        <table-pagination
          title="shops"
          :items="filterdItems"
          @get-paged-items="getPagedItems"
        >
          <table class="table-control">
            <thead>
              <tr>
                <base-table-head
                  :head-items="[
                    'ID',
                    'Shop Name',
                    'Buy Step Link',
                    'Currency',
                    'Status',
                    'Edit',
                  ]"
                ></base-table-head>
              </tr>
            </thead>
            <tbody class="table-control">
              <shop-item
                v-for="shop in pagedItems"
                :key="shop.id"
                :id="shop.id"
                :name="shop.name"
                :buyStepLink="shop.buyStepLink"
                :currency="shop.currency"
                :status="shop.status"
              ></shop-item>
            </tbody>
          </table>
        </table-pagination>
      </div>
    </div>
  </div>
  {{ shops }}
</template>

<script>
import ShopItem from "../../components/shops/ShopItem.vue";
import BaseButton from "../../components/UI/BaseButton.vue";
import BaseTableHead from "../../components/UI/BaseTableHead.vue";
import TablePagination from "../../components/UI/TablePagination.vue";

export default {
  components: { ShopItem, BaseButton, BaseTableHead, TablePagination },
  data() {
    return {
      filterType: "shopName",
      filterValue: null,
      pagedItems: null,
      isloading: false,
    };
  },

  computed: {
    filter() {
      return { type: this.filterType, value: this.filterValue };
    },
    shops() {
      return this.$store.getters["shops/shops"];
    },
    filterdItems() {
      return this.$store.getters["shops/getShopsByFilter"](this.filter);
    },
  },
  methods: {
    getPagedItems(pagedItems) {
      this.pagedItems = pagedItems;
    },
  },
};
</script>

<style scoped></style>
