<template>
  <form-card
    :updateBtn="false"
    @save="submitForm()"
    @delete="tryDeleteTag()"
    :deleteBtn="false"
    @update="null"
  >
    <form @submit.prevent="null">
      <!-- Rows 1  -->
      <div class="flex items-center mt-2">
        <div class="flex-1 mr-2">
          <div class="py-2">
            <label for="platform">ID</label>
            <p>{{ tagId }}</p>
            <!-- <p>{{ tagId === "create" ? "" : tagId }}</p> -->
          </div>

          <div class="py-2">
            <label for="platform">Tag Name</label>
            <div class=" ">
              <input
                class="  form-control "
                :class="{ 'border-red-500': !tagName.isValid }"
                @input="checkTagName"
                type="text"
                placeholder="Tag"
                v-model.trim="tagName.val"
              />
            </div>

            <p class="text-red-500 font-bold" v-if="!tagName.isValid">
              Tag name can't be omitted, please try again
            </p>
          </div>
        </div>
      </div>
    </form>
  </form-card>
  <div v-if="shouldShowLog">
    <div class="text-xl text-red-700" v-if="!!error">
      <p>{{ error }}</p>
    </div>
    {{ tagName.val }}
    <hr />
    {{ tags }}
    <hr />
  </div>
  <toast
    :show="!!error"
    fixed
    :message="error"
    mode="error"
    @close="closeError()"
  />
  <toast :show="isLoading" mode="loading" message="กำลังบันทึก" @close="null" />
  <data-model :show="false"></data-model>
  <load-spinner :show="false"></load-spinner>
  <confirm-modal
    :show="deleteModal"
    @confirm="confirmDelete"
    @close="deleteModal = false"
    title="ยืนยันการลบข้อมูล"
    message="คุณต้องลบข้อมูล Tag ออกจากระบบใช่หรือไม่"
  ></confirm-modal>
</template>

<script>
import FormCard from "../../components/UI/FormCard.vue";
import DataModel from "../../components/UI/DataModal.vue";
import LoadSpinner from "../../components/UI/LoadSpinner.vue";

export default {
  props: {
    tagId: {
      required: false,
      default: null,
    },
    mode: {
      type: String,
      required: true,
      default: null,
    },
  },
  created() {
    if (this.mode === "update") {
      this.setupForm();
    }
  },
  components: { "form-card": FormCard, DataModel, LoadSpinner },
  data() {
    return {
      deleteModal: false,
      tagName: {
        val: "",
        isValid: true,
      },
      formValid: true,
      error: null,
      isLoading: false,
    };
  },
  computed: {
    shouldShowLog() {
      return process.env.VUE_APP_SHOW_LOGS === "1";
    },
    tags() {
      return this.$store.getters["tags/tags"];
    },
    tagData() {
      return this.$store.getters["tags/getTagById"](this.tagId);
    },
  },
  methods: {
    async confirmDelete() {
      // Delete Tag
      try {
        await this.$store.dispatch("tags/deleteTag", {
          tagId: this.tagId,
        });
      } catch (e) {
        this.error = e.message;
      }
      this.$router.replace({
        name: "tags-list",
        params: { delete: true },
      });
    },
    tryDeleteTag() {
      this.deleteModal = true;
    },
    closeError() {
      this.error = null;
    },
    setupForm() {
      console.log(this.tagData);
      if (this.tagData) {
        this.tagName.val = this.tagData.name;
      }
    },
    isFormValid() {
      this.checkTagName();
    },
    checkTagName() {
      console.log("checkTagName fired");
      console.log(this.tagName.val);
      this.tagName.isValid = true;
      if (!this.tagName.val) {
        this.tagName.isValid = false;
        this.formValid = false;
      }
    },
    async submitForm() {
      this.isLoading = true;
      this.formValid = true;
      this.isFormValid();
      if (!this.formValid) {
        return;
      }

      if (this.mode === "update") {
        const submittedData = {
          tagName: this.tagName.val,
          id: this.tagId,
        };
        try {
          await this.$store.dispatch("tags/updateTag", submittedData);
        } catch (e) {
          this.error = e.message;
        }
      } else if (this.mode === "add") {
        const submittedData = {
          tagName: this.tagName.val,
        };
        try {
          await this.$store.dispatch("tags/addTag", submittedData);
        } catch (e) {
          this.error = e.message;
        }
      } else {
        this.error = "submit Form Error because update/add mode";
      }

      this.isLoading = false;

      if (this.error) {
        return;
      }
      this.$router.replace({
        name: "tags-list",
        params: { success: true },
      });
    },
  },
};
</script>
