import { db } from "../../../js/firebaseDb.js";
import { isLogVisible } from "../../../js/debug.js";

export default {
  async updateProduct(context, submittedData) {
    const id = submittedData.id;
    const newProduct = {
      name: submittedData.name,
      device: submittedData.device,
      tags: submittedData.tags,
      status: submittedData.status,
    };

    // send data to firebase
    var prodRef = db.collection(`products`).doc(id);

    // Set all field of the Quota
    await prodRef.update(newProduct).catch((error) => {
      // The document probably doesn't exist.
      console.error("Error updating document: ", error);
    });

    newProduct.id = id;
    const products = context.getters.products;
    const selectedProduct = products.find((product) => product.id === id);
    var index = products.indexOf(selectedProduct);

    if (index !== -1) {
      products.splice(index, 1, newProduct);
      console.log("updated Product : " + index);
    }

    context.commit("setProduct", products);
  },

  async addProduct(context, submittedData) {
    const payload = {
      name: submittedData.name,
      device: submittedData.device,
      tags: submittedData.tags,
      status: submittedData.status,
    };

    // Add new Product to Database
    const prodColl = db.collection(`products`);
    const response = await prodColl.add(payload).catch((error) => {
      console.error("Error adding document: ", error);
    });
    if (response.empty) {
      const error = new Error("Can't send data to server");
      console.log(error);
      throw error;
    }

    // Add new Product to Vuex
    payload.id = response.id;
    context.commit("addProduct", {
      ...payload,
    });
  },

  async setProducts(context) {
    const prodColl = db.collection(`products`);
    const responseData = await prodColl.get().catch((error) => {
      console.error("Error getting documents: ", error);
    });

    const products = [];
    responseData.forEach((doc) => {
      const newProduct = {
        id: doc.id,
        name: doc.data().name,
        device: doc.data().device,
        tags: doc.data().tags,
        status: doc.data().status,
      };
      products.push(newProduct);
    });
    context.commit("setProduct", products.reverse());
  },

  async deleteProduct(context, payload) {
    const prodId = payload.id;
    const theProduct = context.getters.getProductById(prodId);

    // update data in Firbase
    const prodColl = db.collection(`products`);
    prodColl
      .doc(prodId)
      .delete()
      .catch((error) => {
        console.error("Error removing document: ", error);
      });

    // update data in VueX
    if (isLogVisible) {
      console.log("start delete Product in VueX" + index);
    }
    const products = context.state.products;
    var index = products.indexOf(theProduct);
    if (index !== -1) {
      products.splice(index, 1);
      console.log("removed" + index);
    }
    context.commit("setProduct", products);
  },
};
