<template>
  <div class="p-2">
    {{ theData }}
    <!-- Content go here -->
    <base-button @click="testObjFirebase">Post</base-button>
    <base-button @click="getFirebase">Get</base-button>
    <page-header title="Offers" subtitle="Offer List"></page-header>
    <base-card>
      <form @submit.prevent="null" class="flex items-center">
        <h4 class="flex-auto mr-4 font-bold">Search</h4>
        <select class="flex-auto mr-4 form-control" v-model="filterType">
          <option value="product">Product</option>
          <option value="shop">Shop</option>
          <option value="status">Status</option>
          <option value="offerId">Offer Id</option>
        </select>
        <input
          type="text"
          class="form-control  mr-4"
          v-model="filterValue"
          list="inputList"
        />
        <datalist id="inputList" v-if="filterType === 'status'">
          <option
            :value="theStatus.value"
            v-for="theStatus in status"
            :key="theStatus.value"
            >{{ theStatus.text }}
          </option>
        </datalist>
        <!-- <base-button>Search</base-button> -->
        <base-button link mode="flat" :to="{ name: 'offers-add' }"
          ><span class="whitespace-nowrap">+ Offers</span></base-button
        >
      </form>
    </base-card>
    <div class="mt-5">
      <table-pagination
        title="offer"
        :items="filteredOffers"
        @get-paged-items="getPagedItems"
      >
        <table class="table-control">
          <thead>
            <tr>
              <base-table-head
                :head-items="[
                  'Product',
                  'Package',
                  'Platform',
                  'Shop',
                  'Time',
                  'Status',
                  'Price',
                  'Stock',
                  'Price List',
                  'Edit',
                ]"
              ></base-table-head>
            </tr>
          </thead>
          <tbody class="table-control">
            <offer-item
              v-for="offer in pagedOffers"
              :id="offer.id"
              :key="offer.id"
              :offerDetail="offer"
            ></offer-item>
          </tbody>
        </table>
      </table-pagination>
    </div>
  </div>
</template>

<script>
import OfferItem from "../../components/offers/OfferItem.vue";
import TablePagination from "../../components/UI/TablePagination.vue";
import BaseTableHead from "../../components/UI/BaseTableHead.vue";
import firebase from "firebase/app";
import "firebase/firestore";
import { db } from "../../js/firebaseDb.js";

export default {
  components: { OfferItem, TablePagination, BaseTableHead },
  data() {
    return {
      filterType: "product",
      filterValue: null,
      pagedOffers: null,
      theData: null,
    };
  },
  computed: {
    offers() {
      return this.$store.getters["offers/offers"];
    },
    isloading() {
      return (
        !this.$store.getters["products/hasProducts"] &&
        !this.$store.getters["packages/hasPackages"] &&
        !this.$store.getters["shops/hasShops"]
      );
    },
    hasProducts() {
      return this.$store.getters["products/hasProducts"];
    },
    hasPackages() {
      return this.$store.getters["packages/hasPackages"];
    },
    hasShops() {
      return this.$store.getters["shops/hasShops"];
    },
    filter() {
      return { type: this.filterType, value: this.filterValue };
    },
    status() {
      return this.$store.getters["offers/status"];
    },
    filteredOffers() {
      return this.$store.getters["offers/getOffersByFilter"](this.filter);
    },
  },
  methods: {
    getPagedItems(pagedItems) {
      this.pagedOffers = pagedItems;
    },
    async getFirebase() {
      const dataColl = db.collection(`data`);
      const ss = await dataColl.get().catch((error) => {
        console.error("Error getting documents: ", error);
      });
      const data = [];
      ss.forEach((doc) => {
        const newPrice = {
          id: doc.id,
          objInArr: doc.data().objInArr,
          type: typeof doc.data().objInArr,
        };

        data.push(newPrice);
      });
      this.theData = data;
    },
    testObjFirebase() {
      var docData = {
        stringExample: "Hello world!",
        booleanExample: true,
        numberExample: 3.14159265,
        dateExample: firebase.firestore.Timestamp.fromDate(
          new Date("December 10, 1815")
        ),
        arrayExample: [5, true, "hello"],
        objInArr: [
          {
            foo: "foo",
            bar: "bar",
            car: "car",
          },
        ],
        nullExample: null,
        objectExample: {
          a: 5,
          b: {
            nested: "foo",
          },
        },
      };
      db.collection("data")
        .doc("two")
        .set(docData)
        .then(() => {
          console.log("Document successfully written!");
        });
    },
  },
};
</script>

<style scoped></style>
