<template>
  <div class="p-2">
    <div>
      <page-header title="Packages" subtitle="Packages List"></page-header>
      <base-card>
        <form @submit.prevent="null" class="flex items-center">
          <h4 class="flex-auto mr-4 font-bold">Search</h4>
          <select class="flex-auto mr-4 form-control" v-model="filterType">
            <option value="product">Product</option>
            <option value="status">Status</option>
            <option value="priceMore">Price more than</option>
            <option value="priceLess">Price less than</option>
          </select>
          <input
            type="text"
            class="form-control  mr-4"
            v-model="filterValue"
            list="inputList"
          />
          <datalist id="inputList" v-if="filterType === 'status'">
            <option
              :value="theStatus.value"
              v-for="theStatus in status"
              :key="theStatus.value"
              >{{ theStatus.text }}
            </option>
          </datalist>
          <!-- <base-button>Search</base-button> -->
          <base-button link mode="flat" :to="{ name: 'packages-add' }"
            ><span class="whitespace-nowrap">+ Packages</span></base-button
          >
        </form>
      </base-card>
      <div class="mt-5">
        <table-pagination
          title="offer"
          :items="filterdPackages"
          @get-paged-items="getPagedItems"
        >
          <table class="table-control">
            <thead>
              <tr>
                <base-table-head
                  :head-items="[
                    'ID',
                    'Product',
                    'Package Name',
                    'Wordpress Link',
                    'Image Link',
                    'Last Updated',
                    'Sale Price',
                    'Status',
                    'Edit',
                  ]"
                ></base-table-head>
              </tr>
            </thead>
            <tbody class="table-control">
              <package-item
                v-for="thePackage in pagedPackages"
                :key="thePackage.id"
                :id="thePackage.id"
                :name="thePackage.name"
                :product="thePackage.product"
                :wpLink="thePackage.wpLink"
                :image="thePackage.imageLink"
                :updateTime="thePackage.updateTime"
                :salePrice="thePackage.salePrice"
                :status="thePackage.status"
              ></package-item>
            </tbody>
          </table>
        </table-pagination>
      </div>
    </div>
  </div>
  {{ packages }}
</template>

<script>
import PackageItem from "../../components/packages/PackageItem.vue";
import BaseButton from "../../components/UI/BaseButton.vue";
import BaseTableHead from "../../components/UI/BaseTableHead.vue";
import TablePagination from "../../components/UI/TablePagination.vue";

export default {
  components: { PackageItem, BaseButton, BaseTableHead, TablePagination },
  data() {
    return {
      filterType: "product",
      filterValue: null,
      pagedPackages: null,
    };
  },
  computed: {
    filter() {
      return { type: this.filterType, value: this.filterValue };
    },
    packages() {
      return this.$store.getters["packages/packages"];
    },
    status() {
      return this.$store.getters["packages/status"];
    },
    filterdPackages() {
      return this.$store.getters["packages/getPackagesByFilter"](this.filter);
    },
  },
  methods: {
    getPagedItems(pagedItems) {
      this.pagedPackages = pagedItems;
    },
  },
};
</script>

<style scoped></style>
