  </div>
</template>

<script>
import { auth } from "../../js/firebaseDb.js";
export default {
  data() {
    return {
      email: {
        val: null,
        isValid: true,
      },
      password: {
        val: null,
        isValid: true,
      },
    };
  },
  created() {},
  methods: {
    adminLogin() {
      console.log("logging in");
      auth
        .signInWithEmailAndPassword(this.email.val, this.password.val)
        .then((userCredential) => {
          if (userCredential.user) {
            this.$store.dispatch("setUser", userCredential.user);
          } else {
            this.$store.dispatch("setUser", null);
          }
          this.$router.replace({
            name: "offers-list",
          });
        })
        .catch((error) => {
          alert(error.message);
        });
      // this.$store.dispatch("updateRole", { userRole: "student" });
      // this.$router.replace({
      //   name: "std-quota-request",
      // });
    },
  },
};
</script>
