<template>
  <base-card>
    <li class="flex justify-between">
      <span>
        <span class="text-blue-600">{{ object }}</span> : {{ description }} At
        <span class="text-indigo-600">{{ time }}</span></span
      >
      <button @click="toggleLogStatus">
        <svg
          v-if="!hasSolved"
          class="w-6 h-6 text-green-600 hover:bg-green-600 hover:text-gray-50"
          fill="none"
          stroke="currentColor"
          viewBox="0 0 24 24"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M5 13l4 4L19 7"
          ></path>
        </svg>
        <svg
          v-else
          class="w-6 h-6"
          fill="none"
          stroke="currentColor"
          viewBox="0 0 24 24"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M15 12H9m12 0a9 9 0 11-18 0 9 9 0 0118 0z"
          ></path>
        </svg>
      </button>
    </li>
  </base-card>
</template>

<script>
export default {
  props: {
    id: {
      type: String,
      required: true,
    },
    object: {
      type: String,
      required: true,
    },
    description: {
      type: String,
      required: true,
    },
    time: {
      type: String,
      required: true,
    },
  },
  computed: {
    hasSolved() {
      return this.$store.getters["logs/getLogsById"](this.id).hasSolved;
    },
  },
  methods: {
    toggleLogStatus() {
      this.$store.dispatch("logs/toggleLogStatusById", this.id);
    },
  },
};
</script>
