export default {
  products(state, getters, rootState, rootGetters) {
    const products = state.products;
    let formattedProduct = new Array();

    for (let product of products) {
      // find tag by tagid (productId)
      let tagNameArr = new Array();
      for (const tagId of product.tags) {
        if (tagId) {
          const tagName = rootGetters["tags/getTagById"](tagId);
          if (tagName) {
            tagNameArr.push(tagName.name);
          }
        }
      }
      formattedProduct.push({
        ...product,
        tagsName: tagNameArr,
      });
    }

    return formattedProduct;
    // return state.products;
  },
  hasProducts(state) {
    return state.products && state.products.length > 0;
  },
  getProductById: (state, getters) => (id) => {
    const products = getters.products;
    return products.find((product) => product.id === id);
  },
  getProductsByName: (state, getters) => (productName) => {
    const regex = new RegExp(`${productName}.*`, "i");
    const filteredProducts = getters.products.filter((product) =>
      regex.test(product.name)
    );
    return filteredProducts;
  },
  getProductsByStatus: (state, getters) => (status) => {
    const regex = new RegExp(`${status}.*`, "i");
    const filteredProducts = getters.products.filter((product) =>
      regex.test(product.status)
    );
    return filteredProducts;
  },
  getProductsByDevice: (state, getters) => (device) => {
    const regex = new RegExp(`${device}.*`, "i");
    const filteredProducts = getters.products.filter((product) =>
      regex.test(product.device)
    );
    return filteredProducts;
  },
  getProductsByTag: (state, getters) => (tag) => {
    const regex = new RegExp(`${tag}.*`, "i");
    const filteredProducts = getters.products.filter((product) => {
      let macthedTag = false;
      for (let tagsName of product.tagsName) {
        macthedTag = regex.test(tagsName);
        if (macthedTag) {
          break;
        }
      }
      return macthedTag;
    });
    return filteredProducts;
  },
  getProductsByFilter: (state, getters) => (filterPayload) => {
    const filterType = filterPayload.type;
    const filterValue = filterPayload.value;

    if (!filterValue) {
      return getters.products;
    }
    if (filterType === "productName") {
      // all filterd Offer return array
      return getters.getProductsByName(filterValue);
    } else if (filterType === "status") {
      return getters.getProductsByStatus(filterValue);
    } else if (filterType === "device") {
      return getters.getProductsByDevice(filterValue);
    } else if (filterType === "tag") {
      return getters.getProductsByTag(filterValue);
    } else if (filterType === "productId") {
      const product = getters.getProductById(filterValue);
      if (!product || product.length === 0) {
        return new Array();
      } else {
        const offerArr = new Array(product);
        return offerArr;
      }
    }
  },
};
