export default {
  tags(state) {
    return state.tags;
  },
  getTagById: (state) => (id) => {
    return state.tags.find((tag) => tag.id === id);
  },
  getTagsByName: (state, getters) => (tagName) => {
    const regex = new RegExp(`${tagName}.*`, "i");
    const filteredTags = getters.tags.filter((tag) => regex.test(tag.name));
    return filteredTags;
  },
  getTagsByFilter: (state, getters) => (filterPayload) => {
    const filterType = filterPayload.type;
    const filterValue = filterPayload.value;

    if (!filterValue) {
      return getters.tags;
    }
    if (filterType === "tagName") {
      return getters.getTagsByName(filterValue);
    } else if (filterType === "tagId") {
      const tag = getters.getTagById(filterValue);
      if (!tag || tag.length === 0) {
        return new Array();
      } else {
        const tagArr = new Array(tag);
        return tagArr;
      }
    }
  },
};
