<template>
  <div class="p-2">
    <div>
      <page-header title="Products" subtitle="Products List"></page-header>
      <base-card>
        <form @submit.prevent="null" class="flex items-center">
          <h4 class="flex-auto mr-4 font-bold">Search</h4>
          <select class="flex-auto mr-4 form-control" v-model="filterType">
            <option value="productId">Product ID</option>
            <option value="productName">Product Name</option>
            <option value="tag">Tag</option>
            <option value="device">Device</option>
            <option value="status">Status</option>
          </select>
          <input type="text" v-model="filterValue" class="form-control  mr-4" />
          <base-button>Search</base-button>
          <base-button link mode="flat" :to="{ name: 'products-add' }"
            ><span class="whitespace-nowrap">+ Products</span></base-button
          >
        </form>
      </base-card>
      <div class="mt-5">
        <table-pagination
          title="products"
          :items="filterdItems"
          @get-paged-items="getPagedItems"
        >
          <table class="table-control">
            <thead>
              <tr>
                <base-table-head
                  :head-items="[
                    'ID',
                    'Product Name',
                    'Device',
                    'Tags',
                    'Status',
                    'Edit',
                  ]"
                ></base-table-head>
              </tr>
            </thead>
            <tbody class="table-control">
              <product-item
                v-for="product in pagedItems"
                :key="product.id"
                :id="product.id"
                :name="product.name"
                :device="product.device"
                :tags="product.tagsName"
                :status="product.status"
              ></product-item>
            </tbody>
          </table>
        </table-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import ProductItem from "../../components/products/ProductItem.vue";
import BaseButton from "../../components/UI/BaseButton.vue";
import BaseTableHead from "../../components/UI/BaseTableHead.vue";
import TablePagination from "../../components/UI/TablePagination.vue";

export default {
  components: {
    "product-item": ProductItem,
    BaseButton,
    TablePagination,
    BaseTableHead,
  },
  data() {
    return {
      filterType: "productName",
      filterValue: null,
      pagedItems: null,
    };
  },
  computed: {
    filter() {
      return { type: this.filterType, value: this.filterValue };
    },
    products() {
      return this.$store.getters["products/products"];
    },
    filterdItems() {
      return this.$store.getters["products/getProductsByFilter"](this.filter);
    },
  },
  methods: {
    getPagedItems(pagedItems) {
      this.pagedItems = pagedItems;
    },
  },
};
</script>

<style scoped></style>
