<template>
  <form-card
    :updateBtn="false"
    @save="submitForm()"
    @delete="tryDeleteShop()"
    :deleteBtn="true"
    @update="null"
  >
    <form @submit.prevent="null">
      <!-- Rows 1  -->
      <div class="flex items-center mt-2">
        <div class="flex-1 mr-2">
          <label for="platform">Shop Name</label>
          <input
            class="form-control"
            :class="{ 'border-red-500': !shop.isValid }"
            type="text"
            placeholder="Shop"
            v-model="shop.val"
          />
          <div v-if="shouldShowLog">
            {{ shop.val }}
          </div>

          <p class="text-red-500 font-bold" v-if="!shop.isValid">
            Shop name can't be omitted, please try again
          </p>
        </div>
        <div class="flex-1 mx-2">
          <label class="font-bold text-sm mb-2 ml-1">Currency</label>
          <div>
            <dropdown-select
              :options="[
                { text: 'THB', value: 'thb' },
                { text: 'USD', value: 'usd' },
                { text: 'EUR', value: 'eur' },
                { text: 'GBP', value: 'gbp' },
              ]"
              label="Currency"
              textBy="text"
              valueBy="value"
              v-model="currency.val"
              :class="{ 'border-red-500': !currency.isValid }"
            ></dropdown-select>
          </div>

          <div v-if="shouldShowLog">
            {{ currency.val }}
          </div>

          <p class="text-red-500 font-bold" v-if="!currency.isValid">
            Currency can't be omitted, please try again
          </p>
        </div>
      </div>

      <div class="mt-2">
        <label for="url">Buy Step Link</label>
        <input
          class="form-control"
          type="text"
          placeholder="Buy Step Link"
          :class="{ 'border-red-500': !buyStepLink.isValid }"
          v-model="buyStepLink.val"
        />
        <p class="text-red-500 font-bold" v-if="!buyStepLink.isValid">
          Link can't be omitted, please try again
        </p>
      </div>

      <div class="mt-2">
        <div class="flex-1">
          <label class="font-bold text-sm mb-2 ml-1">Status</label>
          <div>
            <dropdown-select
              :options="[
                { text: 'open', value: 'open' },
                { text: 'close', value: 'close' },
                { text: 'error', value: 'error' },
              ]"
              label="Status"
              textBy="text"
              valueBy="value"
              v-model="status.val"
              :class="{ 'border-red-500': !currency.isValid }"
            ></dropdown-select>
          </div>

          <p class="text-red-500 font-bold" v-if="!currency.isValid">
            Currency can't be omitted, please try again
          </p>
        </div>
      </div>
    </form>
  </form-card>
  <confirm-modal
    :show="deleteModal"
    @confirm="confirmDelete"
    @close="deleteModal = false"
    title="ยืนยันการลบข้อมูล"
    message="คุณต้องลบข้อมูล ร้านค้า ออกจากระบบใช่หรือไม่"
  ></confirm-modal>
  {{ shopData }}
</template>

<script>
import FormCard from "../../components/UI/FormCard.vue";
import DropdownSelect from "../../components/UI/DropdownSelect.vue";
export default {
  props: {
    shopId: {
      required: false,
      default: null,
    },
    mode: {
      type: String,
      required: true,
      default: null,
    },
  },
  components: { "form-card": FormCard, "dropdown-select": DropdownSelect },
  data() {
    return {
      // user Input
      buyStepLink: {
        val: "",
        isValid: true,
      },
      currency: {
        val: "",
        isValid: true,
      },
      shop: {
        val: "",
        isValid: true,
      },
      status: {
        val: "",
        isValid: true,
      },

      //   System
      deleteModal: false,
      formValid: true,
      error: null,
      isLoading: false,
    };
  },
  created() {
    if (this.mode === "update") {
      console.log("setting initial value");
      this.setupForm();
    } else if (this.mode === "add") {
      this.status.val = "open";
    }
  },
  computed: {
    shouldShowLog() {
      return process.env.VUE_APP_SHOW_LOGS === "1";
    },
    shops() {
      return this.$store.getters["shops/shops"];
    },
    shopData() {
      return this.$store.getters["shops/getShopById"](this.shopId);
    },
  },
  methods: {
    async confirmDelete() {
      // Delete Tag
      try {
        await this.$store.dispatch("shops/deleteShop", {
          id: this.shopId,
        });
      } catch (e) {
        this.error = e.message;
      }
      this.$router.replace({
        name: "shops-list",
        params: { delete: true },
      });
    },
    tryDeleteShop() {
      this.deleteModal = true;
    },
    closeError() {
      this.error = null;
    },
    setupForm() {
      console.log(this.shopData);
      if (this.shopData) {
        this.shop.val = this.shopData.name;
        this.currency.val = this.shopData.currency;
        this.buyStepLink.val = this.shopData.buyStepLink;
        this.status.val = this.shopData.status;
      }
    },
    isFormValid() {
      this.checkShopName();
      //   check อื่นๆ
    },
    checkShopName() {
      console.log("checkShopName fired");
      this.shop.isValid = true;
      if (!this.shop.val) {
        this.shop.isValid = false;
        this.formValid = false;
      }
    },
    async submitForm() {
      this.formValid = true;
      this.isFormValid();
      if (!this.formValid) {
        return;
      }
      this.$store.dispatch("updateToast", {
        mode: "loading",
        message: "กำลังทำการบันทึก",
        show: true,
      });

      if (this.mode === "update") {
        console.log("start update shop sending.. to vuex");
        const submittedData = {
          name: this.shop.val,
          currency: this.currency.val,
          status: this.status.val,
          buyStepLink: this.buyStepLink.val,
          id: this.shopId,
        };
        try {
          await this.$store.dispatch("shops/updateShop", submittedData);
        } catch (e) {
          this.error = e.message;
        }
      } else if (this.mode === "add") {
        console.log("start add shop sending.. to vuex");
        const submittedData = {
          name: this.shop.val,
          currency: this.currency.val,
          status: this.status.val,
          buyStepLink: this.buyStepLink.val,
        };
        try {
          await this.$store.dispatch("shops/addShop", submittedData);
        } catch (e) {
          this.error = e.message;
        }
      } else {
        this.error = "submit Form Error because update/add mode";
      }

      await this.$store.dispatch("clearToast");
      await this.$store.dispatch("updateToast", {
        mode: "success",
        message: "บันทึกเสร็จสิ้นแล้ว",
        show: true,
      });

      if (this.error) {
        return;
      }
      this.$router.replace({
        name: "shops-list",
      });
    },
  },
};
</script>
