export default {
  shops(state) {
    return state.shops;
  },
  hasShops(state) {
    return state.shops && state.shops.length > 0;
  },
  getShopById: (state) => (id) => {
    return state.shops.find((shop) => shop.id === id);
  },
  getShopsByName: (state, getters) => (name) => {
    const regex = new RegExp(`${name}.*`, "i");
    const filteredShops = getters.shops.filter((shop) => regex.test(shop.name));
    return filteredShops;
  },
  getProductsByCurrency: (state, getters) => (currency) => {
    const regex = new RegExp(`${currency}.*`, "i");
    const filteredShops = getters.shops.filter((shop) =>
      regex.test(shop.currency)
    );
    return filteredShops;
  },
  getProductsByStatus: (state, getters) => (status) => {
    const regex = new RegExp(`${status}.*`, "i");
    const filteredShops = getters.shops.filter((shop) =>
      regex.test(shop.status)
    );
    return filteredShops;
  },
  getShopsByFilter: (state, getters) => (filterPayload) => {
    const filterType = filterPayload.type;
    const filterValue = filterPayload.value;

    if (!filterValue) {
      return getters.shops;
    }
    if (filterType === "shopName") {
      return getters.getShopsByName(filterValue);
    } else if (filterType === "status") {
      return getters.getProductsByStatus(filterValue);
    } else if (filterType === "currency") {
      return getters.getProductsByCurrency(filterValue);
    } else if (filterType === "shopId") {
      const shop = getters.getShopById(filterValue);
      if (!shop || shop.length === 0) {
        return new Array();
      } else {
        const offerArr = new Array(shop);
        return offerArr;
      }
    }
  },
};
