import { createRouter, createWebHistory } from "vue-router";

import OffersList from "./pages/offers/OffersList.vue";
import CreateOffer from "./pages/offers/CreateOffer.vue";
import PricesList from "./pages/offers/PricesList.vue";
import OfferDetail from "./pages/offers/OfferDetail.vue";
import OfferMain from "./pages/offers/OfferMain.vue";

import ShopsList from "./pages/shops/ShopsList.vue";
import CreateShop from "./pages/shops/CreateShop.vue";
import ShopDetail from "./pages/shops/ShopDetail.vue";
import ShopMain from "./pages/shops/ShopMain.vue";

import TagsList from "./pages/tags/TagsList.vue";
import CreateTag from "./pages/tags/CreateTag.vue";
import TagDetail from "./pages/tags/TagDetail.vue";
import TagMain from "./pages/tags/TagMain.vue";

import PackageMain from "./pages/packages/PackageMain.vue";
import PackagesList from "./pages/packages/PackagesList.vue";
import CreatePackage from "./pages/packages/CreatePackage.vue";
import PackageDetail from "./pages/packages/PackageDetail.vue";

import ProductMain from "./pages/products/ProductMain.vue";
import ProductsList from "./pages/products/ProductsList.vue";
import CreateProduct from "./pages/products/CreateProduct.vue";
import ProductDetail from "./pages/products/ProductDetail.vue";

import AdminMain from "./pages/auth/AdminMain.vue";
import AdminLogin from "./pages/auth/Login.vue";
import notificationList from "./pages/notification/NotificationList.vue";
import NotFound from "./pages/NotFound.vue";

// import store from "./store/index.js";

import { auth } from "./js/firebaseDb.js";

const router = createRouter({
  history: createWebHistory(),
  routes: [
    {
      path: "/",
      redirect: { name: "admin-login" },
    },
    {
      path: "/login",
      name: "admin-login",
      component: AdminLogin,
      beforeEnter: (to, from, next) => {
        auth.onAuthStateChanged((user) => {
          if (user) {
            next({ name: "offers-list" });
          } else {
            next();
          }
        });
      },
    },
    {
      path: "/admin",
      name: "admin",
      component: AdminMain,
      meta: { isAdmin: true },
      children: [
        { path: "", redirect: "/offers" },
        {
          path: "/offers",
          name: "offers",
          component: OfferMain,
          children: [
            {
              path: "",
              redirect: { name: "offers-list" },
            },
            {
              path: "list",
              name: "offers-list",
              component: OffersList,
            },
            {
              path: "add",
              name: "offers-add",
              component: CreateOffer,
            },
            {
              path: "update/:id",
              props: true,
              name: "offers-update",
              component: OfferDetail,
            },
            {
              path: "update/:id/prices-list",
              props: true,
              name: "offers-prices-list",
              component: PricesList,
            },
          ],
        },

        { path: "/notification", component: notificationList },
        {
          path: "/packages",
          name: "packages",
          component: PackageMain,
          children: [
            {
              path: "",
              redirect: { name: "packages-list" },
            },
            {
              path: "list",
              name: "packages-list",
              component: PackagesList,
            },
            {
              path: "add",
              name: "packages-add",
              component: CreatePackage,
            },
            {
              path: "update/:id",
              props: true,
              name: "packages-update",
              component: PackageDetail,
            },
          ],
        },
        {
          path: "/products",
          name: "products",
          component: ProductMain,
          children: [
            {
              path: "",
              redirect: { name: "products-list" },
            },
            {
              path: "list",
              name: "products-list",
              component: ProductsList,
            },
            {
              path: "add",
              name: "products-add",
              component: CreateProduct,
            },
            {
              path: "update/:id",
              props: true,
              name: "products-update",
              component: ProductDetail,
            },
          ],
        },
        {
          path: "/shops",
          name: "shops",
          component: ShopMain,
          children: [
            {
              path: "",
              redirect: { name: "shops-list" },
            },
            {
              path: "list",
              name: "shops-list",
              component: ShopsList,
            },
            {
              path: "add",
              name: "shops-add",
              component: CreateShop,
            },
            {
              path: "update/:id",
              props: true,
              name: "shops-update",
              component: ShopDetail,
            },
          ],
        },
        {
          path: "/tags",
          name: "tags",
          component: TagMain,
          children: [
            {
              path: "",
              redirect: { name: "tags-list" },
            },
            {
              path: "list",
              name: "tags-list",
              component: TagsList,
            },
            {
              path: "add",
              name: "tags-add",
              component: CreateTag,
            },
            {
              path: "update/:id",
              props: true,
              name: "tags-update",
              component: TagDetail,
            },
          ],
        },
      ],
      beforeEnter(to, from, next) {
        auth.onAuthStateChanged((user) => {
          console.log("onAuthStateChanged");
          if (user) {
            console.log("this is login user");
            next();
          } else {
            console.log("this is unlogin user");
            next({ name: "admin-login" });
          }
        });
      },
    },

    // { path: "/tags/create", component: CreateTag },
    // { path: "/tags/:id", props: true, component: TagDetail },
    { path: "/:notFound(.*)", component: NotFound },
  ],
});

export default router;
