export default {
  offers(state, getters, rootState, rootGetters) {
    let formattedOffers = [];

    let offers = state.offers;
    for (const offer of offers) {
      console.log(offer.packageId);

      // find package by offers (packageId)
      console.log(rootGetters["packages/packages"]);
      const thePackage = rootGetters["packages/getPackagesById"](
        offer.packageId
      );

      console.log(thePackage.productId);
      // find product by package (productId)
      const theProduct = rootGetters["products/getProductById"](
        thePackage.productId
      );

      // find product by package (productId)
      const theShop = rootGetters["shops/getShopById"](offer.shopId);

      // const lastPrice = offer.pricesList.pop();
      console.log(offer);

      formattedOffers.push({
        ...offer,
        package: thePackage.name,
        productId: thePackage.productId,
        product: theProduct.name,
        shop: theShop.name,
      });

      // if (lastPrice) {
      //   formattedOffers.price = lastPrice.price;
      //   formattedOffers.time = lastPrice.time;
      //   formattedOffers.stock = lastPrice.stock;
      // }
    }

    return formattedOffers;

    // return state.offers;
  },
  hasOffers(state) {
    console.log(state.offers);
    console.log(state.offers.length);
    return state.offers && state.offers.length > 0;
  },
  getOffersByFilter: (state, getters) => (filterPayload) => {
    console.log("get offers by fliter");
    const filterType = filterPayload.type;
    const filterValue = filterPayload.value;

    // return all offers if user  not provide data
    if (!filterValue) {
      return getters.offers;
    }

    // all filterd Offer return array
    if (filterType === "product") {
      return getters.getOffersByName(filterValue);
    } else if (filterType === "shop") {
      return getters.getOffersByShop(filterValue);
    } else if (filterType === "status") {
      return getters.getOffersByStatus(filterValue);
    } else if (filterType === "offerId") {
      const offer = getters.getOffersById(filterValue);
      if (!offer || offer.length === 0) {
        return new Array();
      } else {
        const offerArr = new Array(offer);
        return offerArr;
      }
    }
    // let filteredOffers = [];
  },
  getOffersByStatus: (state, getters) => (status) => {
    const regex = new RegExp(`${status}.*`, "i");
    const offers = getters.offers;
    const filteredOffers = offers.filter((offer) => regex.test(offer.status));
    return filteredOffers;
  },
  getOffersByShop: (state, getters) => (shopName) => {
    const regex = new RegExp(`${shopName}.*`, "i");
    const offers = getters.offers;
    const filteredOffers = offers.filter((offer) => regex.test(offer.shop));
    return filteredOffers;
  },
  getOffersByName: (state, getters) => (productName) => {
    const regex = new RegExp(`${productName}.*`, "i");
    const offers = getters.offers;
    const filteredOffers = offers.filter((offer) => regex.test(offer.product));
    return filteredOffers;
  },
  getOffersById: (state, getters) => (id) => {
    const offers = getters.offers;
    return offers.find((offer) => offer.id === id);
  },
  platforms(state) {
    return state.platforms;
  },
  status(state) {
    return state.status;
  },

  pricesList(state) {
    return state.pricesList;
  },
};
