<template>
  This is Package main

  <div v-if="!isloading">
    <toast
      :show="isDelete"
      mode="info"
      message="ลบข้อมูลเสร็จสิ้นแล้ว"
      @close="closeDelete()"
    />

    <toast
      :show="!!error"
      fixed
      :message="error"
      mode="error"
      @close="closeError()"
    />
  </div>

  <div v-if="isloading" class="flex justify-center items-center h-1/3">
    <base-spinner> </base-spinner>
  </div>
  <router-view v-else></router-view>
  <!-- <div v-if="logVisible">
    {{ products }}
  </div> -->
</template>

<script>
import { isLogVisible } from "../../js/debug.js";
import LoadDataMixin from "../../mixins/loadData.js";

export default {
  mixins: [LoadDataMixin], // loadTag() loadProduct()
  data() {
    return {
      isloading: false,
      error: null,
      isDelete: false,
    };
  },

  created() {
    this.loadData();
  },
  computed: {
    // products() {
    //   return this.$store.getters["products/products"];
    // },
    logVisible() {
      return isLogVisible();
    },
  },
  watch: {
    $route: "fetchData",
  },

  methods: {
    async loadData() {
      this.isloading = true;
      console.log("start Load Product from server");
      await this.loadTag();
      await this.loadProduct();
      console.log("End Load Product from server");
      this.isloading = false;
    },

    closeError() {
      this.error = null;
    },
    closeDelete() {
      this.isDelete = false;
    },
    fetchData() {
      this.isDelete = this.$route.params.delete == "true" ? true : false;
    },
  },
};
</script>
