<template>
  <form-card
    :updateBtn="false"
    @save="submitForm()"
    @delete="tryDeleteProduct()"
    :deleteBtn="false"
    @update="null"
  >
    <form @submit.prevent="null">
      <!-- Rows 1  -->
      <div class="flex items-center">
        <div class="flex-1 mr-2">
          <label for="platform">Product Name</label>
          <input
            type="text"
            name=""
            placeholder="Name"
            class="form-control"
            v-model="name.val"
          />
          <p class="text-red-500 font-bold" v-if="!name.isValid">
            Product name can't be omitted, please try again
          </p>
        </div>

        <div class="flex-1 mr-2">
          <label for="platform">Device</label>
          <input
            type="text"
            name=""
            placeholder="Device"
            class="form-control"
            v-model="device.val"
          />
          <p class="text-red-500 font-bold" v-if="!device.isValid">
            Device can't be omitted, please try again
          </p>
        </div>
        <div class="flex-1 mx-2">
          <label class="font-bold text-sm mb-2 ml-1">Status</label>
          <div>
            <dropdown-select
              :options="[
                { text: 'open', value: 'open' },
                { text: 'close', value: 'close' },
                { text: 'paused', value: 'paused' },
                { text: 'preorder', value: 'preorder' },
              ]"
              label="Currency"
              textBy="text"
              valueBy="value"
              v-model="status.val"
              :class="{ 'border-red-500': !status.isValid }"
            ></dropdown-select>
          </div>
        </div>
      </div>

      <div>
        <label for="url">Tag</label>
        <div class="bg-white rounded-md">
          <multiselect
            v-model="tags.val"
            mode="tags"
            placeholder="Select Tags"
            trackBy="name"
            label="name"
            :searchable="true"
            :options="tagsList"
          >
            <template v-slot:tag="{ option, handleTagRemove, disabled }">
              <div class="multiselect-tag is-user">
                {{ option.name }}
                <i
                  v-if="!disabled"
                  @click.prevent
                  @mousedown.prevent.stop="handleTagRemove(option, $event)"
                />
              </div>
            </template>
          </multiselect>
        </div>
        {{ tags.val }}
      </div>
    </form>
  </form-card>
  <confirm-modal
    :show="deleteModal"
    @confirm="confirmDelete"
    @close="deleteModal = false"
    title="ยืนยันการลบข้อมูล"
    message="คุณต้องลบข้อมูล ร้านค้า ออกจากระบบใช่หรือไม่"
  ></confirm-modal>
</template>

<script>
import FormCard from "../UI/FormCard.vue";
import Multiselect from "@vueform/multiselect";
import { isLogVisible } from "../../js/debug.js";

export default {
  props: {
    productId: {
      required: false,
      default: null,
    },
    mode: {
      type: String,
      required: true,
      default: null,
    },
  },
  components: {
    "form-card": FormCard,
    multiselect: Multiselect,
  },
  data() {
    return {
      // user Input
      name: {
        val: "",
        isValid: true,
      },
      device: {
        val: "",
        isValid: true,
      },
      status: {
        val: "",
        isValid: true,
      },
      tags: {
        val: [],
        isValid: true,
      },

      //   System
      deleteModal: false,
      formValid: true,
      error: null,
      isLoading: false,
    };
  },
  created() {
    if (this.mode === "update") {
      console.log("setting initial value");
      this.setupForm();
    } else if (this.mode === "add") {
      this.status.val = "open";
    }
  },
  computed: {
    tagsList() {
      const tags = this.$store.getters["tags/tags"];
      const theTags = tags.map((item) => ({ name: item.name, value: item.id }));
      return theTags;
    },
    shouldShowLog() {
      return isLogVisible();
    },
    products() {
      return this.$store.getters["products/products"];
    },
    productData() {
      return this.$store.getters["products/getProductById"](this.productId);
    },
  },
  methods: {
    async confirmDelete() {
      // Delete Tag
      try {
        await this.$store.dispatch("products/deleteProduct", {
          id: this.productId,
        });
      } catch (e) {
        this.error = e.message;
      }
      this.$router.replace({
        name: "products-list",
        params: { delete: true },
      });
    },
    tryDeleteProduct() {
      this.deleteModal = true;
    },
    closeError() {
      this.error = null;
    },
    setupForm() {
      console.log(this.productData);
      if (this.productData) {
        this.name.val = this.productData.name;
        this.device.val = this.productData.device;
        this.status.val = this.productData.status;
        this.tags.val = this.productData.tags;
      }
    },
    isFormValid() {
      this.checkProductName();
      //   check อื่นๆ
    },
    checkProductName() {
      console.log("checking ProductName");
      this.name.isValid = true;
      if (!this.name.val) {
        this.name.isValid = false;
        this.formValid = false;
      }
    },
    async submitForm() {
      this.formValid = true;
      this.isFormValid();
      if (!this.formValid) {
        return;
      }
      this.$store.dispatch("updateToast", {
        mode: "loading",
        message: "กำลังทำการบันทึก",
        show: true,
      });

      if (this.mode === "update") {
        console.log("start update product sending.. to vuex");
        const submittedData = {
          name: this.name.val,
          device: this.device.val,
          status: this.status.val,
          tags: this.tags.val,
          id: this.productId,
        };
        try {
          await this.$store.dispatch("products/updateProduct", submittedData);
        } catch (e) {
          this.error = e.message;
        }
      } else if (this.mode === "add") {
        console.log("start add product sending.. to vuex");
        const submittedData = {
          name: this.name.val,
          device: this.device.val,
          status: this.status.val,
          tags: this.tags.val,
        };
        try {
          await this.$store.dispatch("products/addProduct", submittedData);
        } catch (e) {
          this.error = e.message;
        }
      } else {
        this.error = "submit Form Error because update/add mode";
      }

      await this.$store.dispatch("clearToast");
      await this.$store.dispatch("updateToast", {
        mode: "success",
        message: "บันทึกเสร็จสิ้นแล้ว",
        show: true,
      });

      if (this.error) {
        return;
      }
      this.$router.replace({
        name: "products-list",
      });
    },
  },
};
</script>

<style src="@vueform/multiselect/themes/default.css"></style>
<style scoped>
.multiselect-tag.is-user {
  padding: 5px 8px;
  border-radius: 22px;
  @apply bg-primary-200;
  margin: 3px 3px 8px;
}

.multiselect-tag.is-user i:before {
  color: #ffffff;
  border-radius: 50%;
}
</style>
