<template>
  <select
    class="form-control "
    :class="{
      'text-gray-400': !modelValue,
      errors: error,
    }"
    :value="modelValue"
    @input="$emit('update:modelValue', $event.target.value)"
  >
    <option :value="null" disabled hidden>Please Select {{ label }}</option>
    <option
      v-for="option in options"
      :value="option[valueBy]"
      :key="option[valueBy]"
    >
      {{ option[textBy] }}
    </option>
  </select>
</template>

<script>
export default {
  emits: ["update:modelValue"],
  props: {
    error: { type: Boolean, required: false, default: false },
    options: { type: Array, required: true },
    textBy: { type: String, required: true },
    valueBy: { type: String, required: true },
    modelValue: { required: false },
    label: { type: String, required: true },
  },
  data() {
    return {};
  },
};
</script>

<style scoped>
.disable-select {
  opacity: 0.6;
}

.errors {
  @apply border-red-500;
}
</style>
