<template>
  <!-- User Section -->
  <div class="flex justify-between border-b border-gray-200 shadow-sm ">
    <!-- left section -->

    <div class="flex items-center">
      <button @click="toggleMenu">
        <svg
          class="w-6 h-6 ml-2 text-gray-500"
          fill="none"
          stroke="currentColor"
          viewBox="0 0 24 24"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M4 6h16M4 12h16M4 18h16"
          ></path>
        </svg>
      </button>

      <h2 class="text-primary-400 text-2xl ml-6">GameHunt</h2>
    </div>

    <!-- right section -->
    <div class="px-4 py-2 flex justify-end items-center ">
      <p class="text-primary mr-4">{{ user.email }}</p>

      <base-button
        mode="flat"
        class=" text-primary-400 mr-4"
        @click="adminLogout()"
        >Logout</base-button
      >
      <!-- <a href="#" class="text-primary">Sign up</a> -->
    </div>
  </div>
</template>

<script>
import { auth } from "../../js/firebaseDb.js";

export default {
  emits: ["toggle-menu"],
  computed: {
    user() {
      return auth.currentUser;
    },
  },
  methods: {
    toggleMenu() {
      this.$emit("toggle-menu");
    },
    adminLogout() {
      auth
        .signOut()
        .then(() => {
          this.$router.replace({
            name: "admin-login",
          });
        })
        .catch((error) => {
          console.error(error);
        });
    },
  },
};
</script>
