  <!-- component -->
  <!-- This is an example component -->
  <div v-if="logVisible">
    <button @click="showToast = true">Test</button>

    <toast
      :show="false"
      mode="loading"
      message="กำลังทำการบันทึก"
      @close="null"
    />

    <toast
      :show="showToast"
      fixed
      message="บันทึกเสร็จสิ้นแล้ว"
