<script>
import { isLogVisible } from "../../js/debug.js";
import LoadDataMixin from "../../mixins/loadData.js";
export default {
  mixins: [LoadDataMixin], // loadTag();
  data() {
    return {
      currency: null,
      isloading: false,
      showToast: false,
      error: null,
      isSuccess: false,
      isDelete: false,
    };
  },

  created() {
    this.loadTag();
  },
  computed: {
    // successParam() {
    //   this.setIsSuccess();
    //   return this.isSuccess;
    // },
    logVisible() {
      return isLogVisible();
    },
  },
  watch: {
    $route: "fetchData",
  },

  methods: {
    closeToast() {
      this.showToast = false;
    },
    closeError() {
      this.error = null;
    },
    closeDelete() {
      this.isDelete = false;
    },
    closeSuccess() {
      this.isSuccess = false;
    },
    fetchData() {
      this.isSuccess = this.$route.params.success == "true" ? true : false;
      this.isDelete = this.$route.params.delete == "true" ? true : false;
    },
  },
};
