import { db } from "../../../js/firebaseDb.js";
import { isLogVisible } from "../../../js/debug.js";
export default {
  async updatePackage(context, submittedData) {
    const id = submittedData.id;
    const newPackage = {
      name: submittedData.name,
      productId: submittedData.productId,
      wpLink: submittedData.wpLink,
      imageLink: submittedData.imageLink,
      updateTime: submittedData.updateTime,
      salePrice: submittedData.salePrice,
      status: submittedData.status,
    };

    var pkRef = db.collection(`packages`).doc(id);

    // Set all field of the Quota
    await pkRef.update(newPackage).catch((error) => {
      // The document probably doesn't exist.
      console.error("Error updating document: ", error);
    });

    newPackage.id = id;
    const packages = context.state.packages;
    const selectedPackage = packages.find((p) => p.id === id);
    var index = packages.indexOf(selectedPackage);

    if (index !== -1) {
      packages.splice(index, 1, newPackage);
      console.log("updated Package : " + index);
    }

    context.commit("setPackage", packages);
  },

  async addPackage(context, submittedData) {
    const payload = {
      name: submittedData.name,
      productId: submittedData.productId,
      wpLink: submittedData.wpLink,
      imageLink: submittedData.imageLink,
      updateTime: submittedData.updateTime,
      salePrice: submittedData.salePrice,
      status: submittedData.status,
    };

    // Update data in Firebase
    if (isLogVisible) {
      console.log("start sending data to firbase");
    }
    const pkColl = db.collection(`packages`);
    const response = await pkColl.add(payload).catch((error) => {
      console.error("Error adding document: ", error);
    });
    if (response.empty) {
      const error = new Error("Can't send data to server");
      console.log(error);
      throw error;
    }

    // Update data in Vuex
    payload.id = response.id;
    if (isLogVisible) {
      console.log(payload);
    }
    context.commit("addPackage", {
      ...payload,
    });
  },

  async setPackages(context) {
    const pkColl = db.collection(`packages`);
    const responseData = await pkColl.get().catch((error) => {
      console.error("Error getting documents: ", error);
    });

    const packages = [];
    responseData.forEach((doc) => {
      const newPackage = {
        id: doc.id,
        name: doc.data().name,
        productId: doc.data().productId,
        wpLink: doc.data().wpLink,
        imageLink: doc.data().imageLink,
        updateTime: doc.data().updateTime,
        salePrice: doc.data().salePrice,
        status: doc.data().status,
      };
      packages.push(newPackage);
    });
    if (isLogVisible) {
      console.log("set packages completed");
    }
    context.commit("setPackage", packages.reverse());
  },

  async deletePackage(context, payload) {
    const packId = payload.id;
    const thePackage = context.getters.sGetPackagesById(packId);

    // update data in Firbase
    const pkColl = db.collection(`packages`);
    pkColl
      .doc(packId)
      .delete()
      .catch((error) => {
        console.error("Error removing document: ", error);
      });

    // update data in VueX
    console.log("start delete Package in VueX" + index);
    const packages = context.state.packages;
    var index = packages.indexOf(thePackage);
    if (index !== -1) {
      packages.splice(index, 1);
      console.log("removed" + index);
    }
    context.commit("setPackage", packages);
  },
};
