/* eslint-disable no-useless-escape */
<template>
  {{ packageId }}
  <form-card
    :updateBtn="true"
    :saveBtn="false"
    @save="saveOffer"
    @update="updateOffer"
  >
    <div class="flex items-center justify-end">
      <div class="flex-1 text-center text-xl mb-2 text-primary-400">
        <!-- {{ id }} -->
      </div>
      <div class="flex-1 flex justify-end" v-if="!isUpdateMode">
        <button @click="removeOfferForm(id)">
          <svg
            class="w-6 h-6 text-gray-600 tracking-wider"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              d="M6 18L18 6M6 6l12 12"
            ></path>
          </svg>
        </button>
      </div>
    </div>
    <form @submit.prevent="null">
      <!-- Rows 1  -->
      <div class="flex items-center">
        <div class="flex-1 mr-2">
          <label for="platform">platform</label>
          <input
            list="platform"
            placeholder="platform"
            type="text"
            class="form-control"
            @blur="uPlatform.isValid = true"
            :class="{
              errors: !uPlatform.isValid,
            }"
            v-model="uPlatform.val"
          />
          <datalist id="platform">
            <option
              :value="platform"
              v-for="platform in platforms"
              :key="platform"
            >
            </option></datalist
          >{{ uPlatform.val }}
        </div>
        <div class="flex-1 mx-2 h-20">
          <label>Stock</label>
          <label class=" items-center block pt-2">
            <input
              type="checkbox"
              class="form-checkbox h-5 w-5 text-gray-600 rounded-sm"
              v-model="uStock.val"
            /><span class="ml-2 text-gray-700">instock</span>
          </label>
          {{ uStock.val }}
        </div>
        <div class="flex-1 mx-2">
          <label class="font-bold text-sm mb-2 ml-1">Shop</label>
          <div>
            <select
              class="form-control"
              v-model="uShop.val"
              :class="{
                errors: !uShop.isValid,
              }"
              @blur="uShop.isValid = true"
            >
              <option disabled selected>select shop</option>
              <option :value="shop.id" v-for="shop in shops" :key="shop.id">{{
                shop.name
              }}</option>
            </select>
          </div>
          {{ uShop.val }}
        </div>
        <div class="flex-1 mx-2">
          <label class="font-bold text-sm mb-2 ml-1">Date</label>
          <input
            type="datetime-local"
            class="form-control"
            v-model="date"
            disabled
          />{{ date }}
        </div>
        <div class="flex-1 mx-2">
          <label class="font-bold text-sm mb-2 ml-1">Status</label>
          <div>
            <select
              class="form-control"
              :class="{
                errors: !uStatus.isValid,
              }"
              @blur="uStatus.isValid = true"
              v-model="uStatus.val"
            >
              <option disabled selected>select status</option>
              <option
                :value="theStatus.value"
                v-for="theStatus in status"
                :key="theStatus.value"
                >{{ theStatus.text }}</option
              >
            </select>
          </div>
          {{ uStatus.val }}
        </div>
        <div class="flex-1 ml-2">
          <label for="price">Price</label>
          <input
            type="text"
            placeholder="price"
            class="form-control"
            v-model="price"
            disabled
          />{{ price }}
        </div>
      </div>

      <div>
        <label for="url">Link</label>
        <input
          class="form-control"
          :class="{
            errors: !uLink.isValid,
          }"
          @blur="uLink.isValid = true"
          type="text"
          placeholder="link"
          v-model="uLink.val"
        />{{ uLink.val }}
      </div>

      <div>
        <label for="affiliateUrl">affiliateUrl</label>
        <input
          class="form-control"
          :class="{
            errors: !uAffLink.isValid,
          }"
          @blur="uAffLink.isValid = true"
          type="text"
          placeholder="Affiliate Link"
          v-model="uAffLink.val"
        />{{ uAffLink.val }}
      </div>

      <div>
        <label for="ScrapeUrl">ScrapeUrl</label>
        <input
          class="form-control"
          :class="{
            errors: !uScrLink.isValid,
          }"
          @blur="uScrLink.isValid = true"
          type="text"
          placeholder="Scrape Link"
          v-model="uScrLink.val"
        />{{ uScrLink.val }}
      </div>
    </form>
  </form-card>
  <toast
    :show="!!error"
    fixed
    :message="error"
    mode="error"
    @close="closeError()"
  />
</template>

<script>
// https://iendeavor.github.io/vue-next-select/#asynchronous-select-header
// wait for learn ....
import FormCard from "../../components/UI/FormCard.vue";

export default {
  inject: ["removeOfferForm"],
  components: { "form-card": FormCard },
  props: {
    id: {
      type: String,
      required: false,
      default: null,
    },
    packageId: {
      type: String,
      required: false,
      default: null,
    },
    offerData: {
      type: Object,
      required: false,
      default: null,
    },
    mode: {
      type: String,
      required: true,
      default: null,
    },
    offerId: {
      type: String,
      required: false,
      default: null,
    },
  },
  data() {
    return {
      // user
      uPlatform: { val: null, isValid: true },
      uStock: { val: true, isValid: true },
      uShop: { val: null, isValid: true },
      uStatus: { val: null, isValid: true },
      uLink: { val: null, isValid: true },
      uAffLink: { val: null, isValid: true },
      uScrLink: { val: null, isValid: true },

      // system
      formValid: true,
      price: null,
      date: null,
      error: null,
    };
  },
  created() {
    if (this.mode === "update") {
      console.log("setting initial value");
      this.setupForm();
    }
  },
  computed: {
    isUpdateMode() {
      return this.mode === "update";
    },
    localTime() {
      return this.$store.getters.localTime;
    },
    platforms() {
      return this.$store.getters["offers/platforms"];
    },
    shops() {
      return this.$store.getters["shops/shops"];
    },
    status() {
      return this.$store.getters["offers/status"];
    },
    cpntData() {
      return {
        packageId: this.packageId,
        platform: this.uPlatform.val,
        stock: this.uStock.val,
        shopId: this.uShop.val,
        date: this.date,
        status: this.uStatus.val,
        link: this.uLink.val,
        afLink: this.uAffLink.val,
        scpLink: this.uScrLink.val,
        price: this.price,
      };
    },
  },

  methods: {
    setupForm() {
      console.log("edit offer id " + this.offerId);
      const theOffer = this.$store.getters["offers/getOffersById"](
        this.offerId
      );
      console.log(theOffer);
      this.uPlatform.val = theOffer.platform;
      this.uStock.val = theOffer.stock;
      this.uShop.val = theOffer.shopId;
      this.uStatus.val = theOffer.status;
      this.uLink.val = theOffer.link;
      this.uAffLink.val = theOffer.afLink;
      this.uScrLink.val = theOffer.scpLink;

      this.price = theOffer.price;
      this.date = theOffer.time;
    },
    isFormValid() {
      this.formValid = true;

      this.uPlatform.isValid = true;
      this.uStock.isValid = true;
      this.uShop.isValid = true;
      this.uStatus.isValid = true;
      this.uLink.isValid = true;
      this.uAffLink.isValid = true;
      this.uScrLink.isValid = true;

      if (!this.uPlatform.val) {
        this.uPlatform.isValid = false;
        this.formValid = false;
      }
      if (!this.uShop.val) {
        this.uShop.isValid = false;
        this.formValid = false;
      }
      if (!this.uStatus.val) {
        this.uStatus.isValid = false;
        this.formValid = false;
      }
      if (!this.uLink.val) {
        this.uLink.isValid = false;
        this.formValid = false;
      }
      if (!this.uAffLink.val) {
        this.uAffLink.isValid = false;
        this.formValid = false;
      }
      if (!this.uScrLink.val) {
        this.uScrLink.isValid = false;
        this.formValid = false;
      }
    },
    saveOffer() {
      // console.log("Save Offer");
      // const submittedData = {
      //   packageId: this.packageId,
      //   platform: this.platform,
      //   shopId: this.shop,
      //   price: 0,
      //   link: this.link,
      //   affiliateLink: this.affiliateLink,
      //   scrapeLink: this.scrapeLink,
      //   time: this.selectedDate,
      //   stock: this.inStock,
      //   status: this.inputStatus,
      //   pricesList: [],
      // };
      // console.log(submittedData);
      // this.$store.dispatch("offers/addOffer", submittedData);
      // console.log("Save Offer Done");
      // ของ Steam ทำให้มันแจ้งว่าเจ๊งถ้า package เป็น 00000
    },
    closeError() {
      this.error = null;
    },
    setFromData(uPlatform, uStock, uShop, uStatus, uLink, uAffLink, uScrLink) {
      // user
      if (uPlatform !== "") {
        this.uPlatform.val = uPlatform;
      }

      this.uStock.val = uStock;
      this.uShop.val = uShop;
      this.uStatus.val = uStatus;
      this.uLink.val = uLink;
      this.uAffLink.val = uAffLink;
      this.uScrLink.val = uScrLink;
    },
    async updateOffer() {
      const link = this.uLink.val;
      const g2aReg = new RegExp(`www.g2a.com`, "i");
      const cdkeysReg = new RegExp(`www.cdkeys.com`, "i");
      const gamivoReg = new RegExp(`www.gamivo.com`, "i");
      const hrkgameReg = new RegExp(`www.hrkgame.com`, "i");
      const gamesrigReg = new RegExp(`www.gamesrig.com`, "i");
      const khaihomReg = new RegExp(`www.khaihom.com`, "i");
      const steamReg = new RegExp(`store.steampowered.com`, "i");
      const enebaReg = new RegExp(`www.eneba.com`, "i");
      const originReg = new RegExp(`www.origin.com`, "i");
      const epicReg = new RegExp(`www.epicgames.com`, "i");
      const ubiReg = new RegExp(`store.ubi.com`, "i");
      const battlenetReg = new RegExp(`shop.battle.net`, "i");
      const gmgReg = new RegExp(`www.greenmangaming.com`, "i");
      const humbleReg = new RegExp(`www.humblebundle.com`, "i");
      const playAsiaReg = new RegExp(`www.play-asia.com`, "i");
      const gplanetUKReg = new RegExp(`uk.gamesplanet.com`, "i");
      const gplanetUSReg = new RegExp(`us.gamesplanet.com`, "i");
      const gamersgateReg = new RegExp(`www.gamersgate.com`, "i");
      const shopeeReg = new RegExp(`shopee.co.th`, "i");
      const gamebilletReg = new RegExp(`www.gamebillet.com`, "i");
      const fanaticalReg = new RegExp(`www.fanatical.com`, "i");
      // shopee
      // gamebillet
      // fanatical
      const gogReg = new RegExp(`www.gog.com`, "i");

      if (g2aReg.test(link)) {
        console.log("this is g2a shop");
        // const prodNoReg = new RegExp("\d{10,}", "i");
        const regex = /\d{10,}/g;
        const found = link.match(regex);
        console.log(found);
        this.setFromData(
          "",
          true,
          "g2a",
          "open",
          link,
          `${link}?gtag=983c4fb33d`,
          `https://www.g2a.com/marketplace/product/auctions/?id=${found}`
        );
      } else if (cdkeysReg.test(link)) {
        this.setFromData(
          "",
          true,
          "cdkeys",
          "open",
          link,
          `${link}?mw_aref=cgh&utm_currency=THB`,
          link
        );
      } else if (gamivoReg.test(link)) {
        this.setFromData(
          "",
          true,
          "gamivo",
          "open",
          link,
          `${link}?glv=yenimkaz`,
          link
        );
      } else if (hrkgameReg.test(link)) {
        this.setFromData(
          "",
          true,
          "hrkgame",
          "open",
          link,
          `${link}#a_aid=cgh`,
          link
        );
      } else if (gamesrigReg.test(link)) {
        this.setFromData("", true, "gamesrig", "open", link, link, link);
      } else if (khaihomReg.test(link)) {
        this.setFromData("", true, "khaihom", "open", link, link, link);
      } else if (steamReg.test(link)) {
        const steamApp = new RegExp(`store.steampowered.com/app/`, "i");
        const steamBundle = new RegExp(`store.steampowered.com/bundle/`, "i");

        // Steam App
        if (steamApp.test(link)) {
          const regex = /app\/(\d{5,})\//i;
          const found = [...link.match(regex)];
          console.log(found);
          this.setFromData(
            "",
            true,
            "steam",
            "open",
            link,
            link,
            `https://store.steampowered.com/api/appdetails/?appids=${found[1]}&cc=TH&packageid=00000`
          );
        } else if (steamBundle.test(link)) {
          //steam Bundle
          this.setFromData(
            "",
            true,
            "steam",
            "open",
            link,
            link,
            `${link}?cc=TH`
          );
        } else {
          console.error("steam pattern regex error");
        }
      } else if (enebaReg.test(link)) {
        this.setFromData("", true, "eneba", "open", link, link, link);
      } else if (originReg.test(link)) {
        this.setFromData(
          "",
          true,
          "origin",
          "pause",
          link,
          link,
          "need to be filled"
        );
      } else if (epicReg.test(link)) {
        this.setFromData(
          "",
          true,
          "epicgames",
          "pause",
          link,
          "need to be filled",
          "need to be filled"
        );
      } else if (ubiReg.test(link)) {
        const regex = /pid=(.*?)&/i;
        const found = [...link.match(regex)];
        console.log(found);
        this.setFromData(
          "",
          true,
          "ubisoftstore",
          "open",
          link,
          `https://store.ubi.com/sea/game?lang=th_TH&pid=${found[1]}`,
          `https://store.ubi.com/sea/game?lang=th_TH&pid=${found[1]}`
        );
      } else if (battlenetReg.test(link)) {
        const regex = /product\/(.*?)\?/i;
        const found = [...link.match(regex)];
        console.log(found);
        // fetch(`https://us.shop.battle.net/api/product/${found[1]}`)
        //   .then((response) => response.json())
        //   .then((data) => console.log(data));

        this.setFromData(
          "",
          true,
          "battlenet",
          "pause",
          link,
          link,
          `https://us.shop.battle.net/api/product/${found[1]}`
        );
      } else if (gmgReg.test(link)) {
        this.setFromData("", true, "greenmangaming", "open", link, link, link);
      } else if (humbleReg.test(link)) {
        this.setFromData(
          "",
          true,
          "humblebundle",
          "open",
          link,
          `${link}?partner=cgh`,
          link
        );
      } else if (playAsiaReg.test(link)) {
        this.setFromData(
          "",
          true,
          "play-asia",
          "open",
          link,
          `${link}?affiliate_id=2741481`,
          link
        );
      } else if (gplanetUKReg.test(link)) {
        this.setFromData(
          "",
          true,
          "gamesplanetUK",
          "open",
          link,
          `${link}?ref=cgh`,
          link
        );
      } else if (gplanetUSReg.test(link)) {
        this.setFromData(
          "",
          true,
          "gamesplanetUS",
          "open",
          link,
          `${link}?ref=cgh`,
          link
        );
      } else if (gamersgateReg.test(link)) {
        this.setFromData("", true, "gamersgate", "open", link, link, link);
      } else if (gogReg.test(link)) {
        this.setFromData("", true, "gog", "open", link, link, link);
      } else if (shopeeReg.test(link)) {
        this.setFromData("", true, "shopee", "pause", link, link, link);
      } else if (gamebilletReg.test(link)) {
        this.setFromData("", true, "gamebillet", "pause", link, link, link);
      } else if (fanaticalReg.test(link)) {
        this.setFromData("", true, "fanatical", "pause", link, link, link);
      } else {
        this.error = "URL pattern not found";
      }
    },
  },
};
</script>
