import { db } from "../../../js/firebaseDb.js";
import { isLogVisible } from "../../../js/debug.js";
import firebase from "firebase/app";
import "firebase/firestore";

export default {
  async updateOffer(context, submittedData) {
    const timestamp = firebase.firestore.Timestamp.fromDate(new Date());
    const id = submittedData.id;
    const newOffer = {
      packageId: submittedData.packageId,
      platform: submittedData.platform,
      shopId: submittedData.shopId,
      link: submittedData.link,
      afLink: submittedData.afLink,
      scpLink: submittedData.scpLink,
      time: timestamp,
      // price: submittedData.price,
      // stock: submittedData.stock,
      status: submittedData.status,
    };

    // === Send data to DB === //
    var offerRef = db.collection(`offers`).doc(id);

    await offerRef.update(newOffer).catch((error) => {
      console.error("Error updating document: ", error);
    });

    // === Send data to VueX === //
    newOffer.id = id;
    const offers = context.state.offers;
    const selectedOffer = offers.find((offer) => offer.id === id);
    var index = offers.indexOf(selectedOffer);

    if (index !== -1) {
      offers.splice(index, 1, newOffer);
      console.log("updated Offer : " + index);
    }
    context.commit("setOffer", offers);
  },

  async addOffer(context, payload) {
    const timestamp = firebase.firestore.Timestamp.fromDate(new Date());
    console.log(payload);
    const offerData = {
      packageId: payload.packageId,
      platform: payload.platform,
      shopId: payload.shopId,
      link: payload.link,
      // affiliateLink: payload.affiliateLink,
      // scrapeLink: payload.scrapeLink,
      afLink: payload.afLink,
      scpLink: payload.scpLink,
      // price: payload.price,
      time: timestamp,
      // stock: payload.stock,
      status: payload.status,
    };

    // Add new Quota to Database
    const offerColl = db.collection(`offers`);
    const response = await offerColl.add(offerData).catch((error) => {
      console.error("Error adding document: ", error);
    });
    if (response.empty) {
      const error = new Error("Can't send data to server");
      console.log(error);
      throw error;
    }

    // Add new Quota to VueX
    offerData.id = response.id;
    console.log(offerData);
    context.commit("addOffer", offerData);
  },

  async setOffer(context) {
    console.log("get Quota data from Firebase");

    // get offer Data
    const offerColl = db.collection(`offers`);
    const offerSnapshot = await offerColl.get().catch((error) => {
      console.error("Error getting documents: ", error);
    });

    if (isLogVisible()) {
      console.log(offerSnapshot);
    }

    if (offerSnapshot.empty) {
      context.commit("clearOffer");
      const error = new Error("No such document!");
      console.log(error);
      throw error;
    }

    const offers = [];
    await offerSnapshot.forEach((doc) => {
      const newOffer = {
        id: doc.id,
        packageId: doc.data().packageId,
        platform: doc.data().platform,
        shopId: doc.data().shopId,
        link: doc.data().link,
        afLink: doc.data().afLink,
        scpLink: doc.data().scpLink,
        status: doc.data().status,
        time: doc.data().time,
        plPrice: doc.data().plPrice,
        plFee: doc.data().plFee,
        plPriceF: doc.data().plPriceF,
        plCurrency: doc.data().plCurrency,
        plStock: doc.data().plStock,
        plTime: doc.data().plTime,
      };

      offers.push(newOffer);
    });

    console.log(offers);
    console.log("set offers completed");
    context.commit("setOffer", offers.reverse());
  },

  async deleteOffer(context, payload) {
    const offerId = payload.id;
    const theOffer = context.getters.getOffersById(offerId);

    // update data in Firbase
    const offerColl = db.collection(`offers`);
    offerColl
      .doc(offerId)
      .delete()
      .catch((error) => {
        console.error("Error removing document: ", error);
      });

    // update data in VueX
    console.log("start delete Package in VueX" + index);
    const offers = context.state.offers;
    var index = offers.indexOf(theOffer);
    if (index !== -1) {
      offers.splice(index, 1);
      console.log("removed" + index);
    }
    context.commit("setOffer", offers);
  },

  async setPricesList(context, payload) {
    console.log("get PricesList data from Firebase");

    const plId = payload.plId;

    console.log(plId);
    // get offer Data

    // const now = new Date();
    // const year = now.toString();
    // const year = now.getFullYear();

    // console.log(year);

    const plRef = db.collection(`offers/${plId}/pricesList/`);
    const plSnapshot = await plRef.get().catch((error) => {
      console.error("Error getting documents: ", error);
    });

    if (isLogVisible()) {
      console.log(plSnapshot);
    }

    if (plSnapshot.empty) {
      const error = new Error("No such document!");
      console.log(error);
      context.commit("clearPricesList");
      throw error;
    }

    // const plArr = doc.data().pricesList;
    const plArr = [];
    await plSnapshot.forEach((doc) => {
      const pl = {
        id: doc.id,
        currency: doc.data().currency,
        fee: doc.data().fee,
        price: doc.data().price,
        priceF: doc.data().priceF,
        stock: doc.data().stock,
        time: doc.data().time,
      };

      plArr.push(pl);
    });

    context.commit("setPricesList", plArr);
  },
};
