import { createStore } from "vuex";
import offersModule from "./modules/offers/index.js";
import packagesModule from "./modules/packages/index.js";
import productsModule from "./modules/products/index.js";
import shopsModule from "./modules/shops/index.js";
import tagsModule from "./modules/tags/index.js";
import logsModule from "./modules/logs/index.js";
import userModule from "./modules/user/index.js";
const store = createStore({
  modules: {
    offers: offersModule,
    packages: packagesModule,
    products: productsModule,
    shops: shopsModule,
    tags: tagsModule,
    logs: logsModule,
    user: userModule,
  },
});

export default store;
