<template>
  <the-header @toggle-menu="ToggleMenu"></the-header>
  <!-- content wrapper -->
  <div class="grid md:grid-cols-12">
    <!-- Nav -->
    <transition name="sidebar">
      <div class=" bg-gray-300 col-span-2 " v-if="showedMenu">
        <the-sidebar></the-sidebar>
      </div>
    </transition>
    <!-- End Nav -->
    <!-- Main Section -->
    <main :class="MainSectionClasses" class="h-screen px-4">
      <router-view></router-view>
    </main>
    <toast
      :mode="theToast.mode"
      :show="theToast.show"
      :message="theToast.message"
      @close="clostToast"
    />
  </div>
  <!-- End content wrapper -->
</template>

<script>
import TheHeader from "../../components/layouts/TheHeader.vue";
import TheSidebar from "../../components/layouts/TheSidebar.vue";

export default {
  components: { TheHeader, TheSidebar },
  data() {
    return {
      showedMenu: true,
    };
  },
  computed: {
    theToast() {
      return this.$store.getters["toast"];
    },
    MainSectionClasses() {
      if (this.showedMenu) {
        return { "col-span-10": true };
      } else {
        return { "col-span-12": true };
      }
    },
  },
  methods: {
    clostToast() {
      this.$store.dispatch("clearToast");
    },
    ToggleMenu() {
      this.showedMenu = !this.showedMenu;
    },
  },
};
</script>

