<template>
  <form-card
    :updateBtn="false"
    @save="submitForm()"
    @delete="tryDeletePackage()"
    :deleteBtn="false"
    @update="null"
  >
    <form @submit.prevent="null">
      <!-- Rows 1  -->
      <div class="flex items-center">
        <div class="flex-1 mr-2">
          <label for="platform">Product</label>
          <search-input
            :options="products"
            placeHolder="เลือก Product"
            :setValue="product.val"
            @selectedValue="selectProd"
            textField="name"
            valueField="id"
            :error="!product.isValid"
          ></search-input>
          {{ product.val }}
          <!-- <input
            type="text"
            name=""
            id="platform"
            placeholder="platform"
            class="form-control"
            v-model="product.val"
          /> -->
        </div>
        <div class="flex-1 mr-2">
          <label for="package">Package Name</label>
          <input
            type="text"
            name=""
            id="package"
            placeholder="package"
            class="form-control"
            v-model="name.val"
          />
        </div>
        <div class="flex-1 mx-2">
          <label class="font-bold text-sm mb-2 ml-1">Status</label>
          <div>
            <select class="form-control" v-model="status.val">
              <option value="open">open</option>
              <option value="close">close</option>
              <option value="paused">paused</option>
              <option value="paused">preorder</option>
            </select>
          </div>
        </div>
        <div class="flex-1 mx-2">
          <label class="font-bold text-sm mb-2 ml-1">Date</label>
          <input
            type="text"
            class="form-control"
            v-model="updateTime.val"
            disabled
          />
        </div>
        <div class="flex-1 mr-2">
          <label for="platform">Sale Price</label>
          <input
            disabled
            type="text"
            name=""
            id="platform"
            placeholder="platform"
            class="form-control"
            v-model="salePrice.val"
          />
        </div>
      </div>

      <div>
        <label for="url">Wordpress Link</label>
        <input
          class="form-control"
          type="text"
          name=""
          id="url"
          placeholder="Wordpress Link"
          v-model="wpLink.val"
        />
      </div>

      <div>
        <label for="affiliateUrl">Image Link</label>
        <input
          class="form-control"
          type="text"
          name=""
          id="affiliateUrl"
          placeholder="Wordpress Link"
          v-model="imageLink.val"
        />
      </div>
    </form>
  </form-card>
  <confirm-modal
    :show="deleteModal"
    @confirm="confirmDelete"
    @close="deleteModal = false"
    title="ยืนยันการลบข้อมูล"
    message="คุณต้องลบข้อมูล Package ออกจากระบบใช่หรือไม่"
  ></confirm-modal>
  {{ packages }}
  {{ products }}
</template>

<script>
import FormCard from "../../components/UI/FormCard.vue";
import { isLogVisible } from "../../js/debug.js";
import SearchInput from "../../components/UI/SearchInput.vue";

export default {
  props: {
    packageId: {
      required: false,
      default: null,
    },
    mode: {
      type: String,
      required: true,
      default: null,
    },
  },
  components: { "form-card": FormCard, SearchInput },
  data() {
    return {
      // User Input
      product: {
        val: null,
        isValid: true,
      },
      name: {
        val: "",
        isValid: true,
      },
      status: {
        val: "",
        isValid: true,
      },
      updateTime: {
        val: "",
        isValid: true,
      },
      salePrice: {
        val: 0,
        isValid: true,
      },
      wpLink: {
        val: "",
        isValid: true,
      },
      imageLink: {
        val: "",
        isValid: true,
      },

      //   System
      deleteModal: false,
      formValid: true,
      error: null,
      isLoading: false,
    };
  },
  created() {
    if (this.mode === "update") {
      console.log("setting initial value");
      this.setupForm();
    } else if (this.mode === "add") {
      this.status.val = "open";
    }
  },

  computed: {
    productsList() {
      const products = this.products;
      const newProducts = products.map((item) => ({
        name: item.name,
        value: item.id,
      }));
      return newProducts;
    },
    shouldShowLog() {
      return isLogVisible();
    },
    products() {
      return this.$store.getters["products/products"];
    },
    packages() {
      return this.$store.getters["packages/packages"];
    },
    packageData() {
      return this.$store.getters["packages/getPackagesById"](this.packageId);
    },
  },
  methods: {
    setupForm() {
      console.log(this.packageData);
      if (this.packageData) {
        this.product.val = this.packageData.productId;
        this.name.val = this.packageData.name;
        this.status.val = this.packageData.status;
        this.updateTime.val = this.packageData.updateTime;
        this.salePrice.val = this.packageData.salePrice;
        this.wpLink.val = this.packageData.wpLink;
        this.imageLink.val = this.packageData.imageLink;
      }
    },
    async confirmDelete() {
      // Delete Tag
      try {
        await this.$store.dispatch("packages/deletePackage", {
          id: this.packageId,
        });
      } catch (e) {
        this.error = e.message;
      }
      this.$router.replace({
        name: "packages-list",
        params: { delete: true },
      });
    },
    tryDeletePackage() {
      this.deleteModal = true;
    },
    closeError() {
      this.error = null;
    },
    isFormValid() {
      this.checkPackageName();
      //   check อื่นๆ
    },
    checkPackageName() {
      console.log("checking Package Name");
      this.name.isValid = true;
      if (!this.name.val) {
        this.name.isValid = false;
        this.formValid = false;
      }
    },
    selectProd(val) {
      this.product.val = val;
    },
    async submitForm() {
      this.formValid = true;
      this.isFormValid();
      if (!this.formValid) {
        return;
      }
      this.$store.dispatch("updateToast", {
        mode: "loading",
        message: "กำลังทำการบันทึก",
        show: true,
      });

      if (this.mode === "update") {
        console.log("start update package sending.. to vuex");
        const submittedData = {
          id: this.packageId,
          productId: this.product.val,
          name: this.name.val,
          status: this.status.val,
          updateTime: this.$store.getters["localTime"],
          salePrice: this.salePrice.val,
          wpLink: this.wpLink.val,
          imageLink: this.imageLink.val,
        };
        try {
          await this.$store.dispatch("packages/updatePackage", submittedData);
        } catch (e) {
          this.error = e.message;
        }
      } else if (this.mode === "add") {
        console.log("start add package sending.. to vuex");
        const submittedData = {
          productId: this.product.val,
          name: this.name.val,
          status: this.status.val,
          updateTime: this.$store.getters["localTime"],
          salePrice: this.salePrice.val,
          wpLink: this.wpLink.val,
          imageLink: this.imageLink.val,
        };
        try {
          await this.$store.dispatch("packages/addPackage", submittedData);
        } catch (e) {
          this.error = e.message;
        }
      } else {
        this.error = "submit Form Error because update/add mode";
      }

      await this.$store.dispatch("clearToast");
      await this.$store.dispatch("updateToast", {
        mode: "success",
        message: "บันทึกเสร็จสิ้นแล้ว",
        show: true,
      });

      if (this.error) {
        return;
      }
      this.$router.replace({
        name: "packages-list",
      });
    },
  },
};
</script>
