<script>
export default {
  data() {
    return {
      selectedMenu: [false, false, false, false, false, false],

      subMenu: [false],
    };
  },
  computed: {
    selectedMenuStyle() {
      return { active: this.selectedMenu };
    },
  },
  methods: {
    toggleSubMenu(index) {
      this.subMenu[index] = !this.subMenu[index];
    },
    selectMenu(index) {
      var newMenu = new Array(this.selectedMenu.length).fill(false);
      newMenu[index] = true;
      this.selectedMenu = newMenu;
    },
    selectMenuAndSub(menuIndex, subIndex) {
      this.toggleSubMenu(subIndex);
      this.selectMenu(menuIndex);
    },
  },
};
</script>

