export default {
  methods: {
    async loadTag(forceUpdate = false) {
      try {
        await this.$store.dispatch("tags/setTags", {
          forceUpdate: forceUpdate,
        });
      } catch (e) {
        this.error = e.message;
      }
    },
    async loadProduct(forceUpdate = false) {
      try {
        await this.$store.dispatch("products/setProducts", {
          forceUpdate: forceUpdate,
        });
      } catch (e) {
        this.error = e.message;
      }
    },
    async loadPackage(forceUpdate = false) {
      try {
        await this.$store.dispatch("packages/setPackages", {
          forceUpdate: forceUpdate,
        });
      } catch (e) {
        this.error = e.message;
      }
    },
    async loadShop(forceUpdate = false) {
      this.isloading = true;
      try {
        await this.$store.dispatch("shops/setShops", {
          forceUpdate: forceUpdate,
        });
      } catch (e) {
        this.error = e.message;
      }

      this.isloading = false;
    },
  },
};
