export default {
  addOffer(state, payload) {
    state.offers.push(payload);
  },
  setOffer(state, payload) {
    state.offers = payload;
  },
  clearOffer(state) {
    state.offers = [];
  },
  setPricesList(state, payload) {
    state.pricesList = payload;
  },
  clearPricesList(state) {
    state.PricesList = [];
  },
};
