<template>
  <base-card>
    <h3 class="text-2xl">Product & Package</h3>
    <div>
      <form action="" class="flex justify-around space-x-4">
        <div class="w-full">
          <label for="product">product</label>
          <serach-input
            :options="products"
            placeHolder="product"
            :setValue="uProduct.val"
            @selectedValue="selectProduct"
            textField="name"
            valueField="id"
            :error="!uProduct.isValid"
            @input="uProduct.isValid = true"
          ></serach-input>

          {{ uProduct.val }}
        </div>
        <div class="w-full">
          <label for="package">package</label>
          <serach-input
            :options="packages"
            placeHolder="package"
            :setValue="uPackage.val"
            @selectedValue="selectPackage"
            textField="name"
            valueField="id"
            :error="!uPackage.isValid"
            @input="uPackage.isValid = true"
          ></serach-input>
          {{ uPackage.val }}
        </div>
      </form>
    </div>
  </base-card>
  <base-card>
    <div class="flex justify-between items-center px-3">
      <h3 class="text-2xl flex-1">Offers</h3>
      <div class="flex-1 flex justify-end "></div>
    </div>

    <update-offer-item
      v-for="formId in offerForm"
      :mode="mode"
      :packageId="uPackage.val"
      :offerId="offerId"
      :key="formId"
      :ref="formId"
      :id="formId"
    ></update-offer-item>

    <div class="flex justify-between my-5" v-if="!isUpdateMode">
      <div></div>
      <button
        @click="addForm"
        class="inline-flex items-center justify-center w-10 h-10  text-gray-100 transition-colors duration-150 bg-gray-700 rounded-full focus:shadow-outline hover:bg-gray-800"
      >
        <svg class="ml-auto mr-auto w-4 h-4 fill-current" viewBox="0 0 20 20">
          <path
            d="M10 3a1 1 0 011 1v5h5a1 1 0 110 2h-5v5a1 1 0 11-2 0v-5H4a1 1 0 110-2h5V4a1 1 0 011-1z"
            clip-rule="evenodd"
            fill-rule="evenodd"
          ></path>
        </svg>
      </button>
      <base-button class="px-10 py-2" @click="saveAllForm"
        >SAVE ALL</base-button
      >
    </div>

    <div class="flex justify-end" v-else>
      <base-button class="px-10 py-2" @click="updateData">SAVE</base-button>
    </div>
  </base-card>
</template>

<script>
import UpdateOfferItem from "../../components/offers/UpdateOfferItem.vue";
import SerachInput from "../../components/UI/SearchInput.vue";
import { getId } from "../../js/utility.js";
export default {
  props: {
    offerId: {
      type: String,
      required: false,
      default: null,
    },
    mode: {
      type: String,
      required: true,
      default: null,
    },
  },
  components: {
    "update-offer-item": UpdateOfferItem,
    "serach-input": SerachInput,
  },
  data() {
    return {
      offerForm: [],
      uProduct: { val: null, isValid: true },
      uPackage: { val: null, isValid: true },
    };
  },
  computed: {
    isUpdateMode() {
      return this.mode === "update";
    },
    formCount() {
      return this.offers.length;
    },
    products() {
      return this.$store.getters["products/products"];
    },
    packages() {
      return this.$store.getters["packages/getPackagesByProductId"](
        this.uProduct.val
      );
    },
  },
  created() {
    if (this.mode === "update") {
      console.log("setting initial value");
      this.setupForm();
    }
    this.addForm();
  },
  provide() {
    return {
      removeOfferForm: this.removeOfferForm,
    };
  },

  methods: {
    setupForm() {
      console.log("edit offer id " + this.offerId);
      const theOffer = this.$store.getters["offers/getOffersById"](
        this.offerId
      );
      console.log(theOffer);
      this.uProduct.val = theOffer.productId;
      this.uPackage.val = theOffer.packageId;
    },
    async callChildMethod(methodName) {
      for (const formId of this.offerForm) {
        await this.$refs[formId][methodName]();
      }
    },

    async updateData() {
      //
    },

    async saveAllForm() {
      await this.callChildMethod("isFormValid");
      const inValidData = this.offerForm.some(
        (formId) => this.$refs[formId].formValid === false
      );
      console.log(inValidData);
      if (inValidData) {
        return;
      }

      //   const submitData = [];

      this.offerForm.forEach((formId) => {
        // console.log(this.$refs[formId].cpntData)
        console.log("Save Offer");
        const newOffer = {
          packageId: this.$refs[formId].cpntData.packageId,
          platform: this.$refs[formId].cpntData.platform,
          shopId: this.$refs[formId].cpntData.shopId,
          link: this.$refs[formId].cpntData.link,
          afLink: this.$refs[formId].cpntData.afLink,
          scpLink: this.$refs[formId].cpntData.scpLink,
          stock: this.$refs[formId].cpntData.stock,
          status: this.$refs[formId].cpntData.status,
        };
        // submitData.push(newOffer);
        this.$store.dispatch("offers/addOffer", newOffer);
        console.log("Save Offer Done");
      });
      //   this.$store.dispatch("offers/addOffer", submitData);
      //   console.log("Save Offer Done");
    },

    addForm() {
      this.offerForm.push(getId());
    },
    removeOfferForm(formId) {
      const removedFormIndex = this.offerForm.findIndex((item) => {
        return item == formId;
      });
      console.log(removedFormIndex);
      this.offerForm.splice(removedFormIndex, 1);
    },
    selectProduct(val) {
      this.uProduct.isValid = true;
      this.uProduct.val = val;
    },
    selectPackage(val) {
      this.uPackage.isValid = true;
      this.uPackage.val = val;
    },
  },
};
</script>

<style scoped></style>
