import { db } from "../../../js/firebaseDb.js";
import { isLogVisible } from "../../../js/debug.js";

export default {
  async updateShop(context, submittedData) {
    const id = submittedData.id;
    const newShop = {
      name: submittedData.name,
      currency: submittedData.currency,
      buyStepLink: submittedData.buyStepLink,
      status: submittedData.status,
    };

    var shopRef = db.collection(`shops`).doc(id);

    // update data in Firebase
    await shopRef.update(newShop).catch((error) => {
      // The document probably doesn't exist.
      console.error("Error updating document: ", error);
    });

    // update data in vuex
    newShop.id = id;
    const shops = context.getters.shops;
    const selectedShop = shops.find((shop) => shop.id === id);
    var index = shops.indexOf(selectedShop);

    if (index !== -1) {
      shops.splice(index, 1, newShop);
      if (isLogVisible) {
        console.log("updated Shop : " + index);
      }
    }

    context.commit("setShop", shops);
  },

  async addShop(context, submittedData) {
    const payload = {
      name: submittedData.name,
      currency: submittedData.currency,
      buyStepLink: submittedData.buyStepLink,
      status: submittedData.status,
    };

    // Add new shop to Database
    const shopColl = db.collection(`shops`);
    const response = await shopColl.add(payload).catch((error) => {
      console.error("Error adding document: ", error);
    });
    if (response.empty) {
      const error = new Error("Can't send data to server");
      console.log(error);
      throw error;
    }

    // Add new shop to Vuex
    payload.id = response.id;
    context.commit("addShop", {
      ...payload,
    });
  },

  async setShops(context) {
    const shopColl = db.collection(`shops`);
    const responseData = await shopColl.get().catch((error) => {
      console.error("Error getting documents: ", error);
    });

    const shops = [];
    responseData.forEach((doc) => {
      const newShop = {
        id: doc.id,
        name: doc.data().name,
        currency: doc.data().currency,
        buyStepLink: doc.data().buyStepLink,
        status: doc.data().status,
      };
      shops.push(newShop);
    });
    context.commit("setShop", shops.reverse());
  },

  async deleteShop(context, payload) {
    const shopId = payload.shopId;
    const theShop = context.getters.getTagById(shopId);

    // update data in Firebase

    const shopColl = db.collection(`shops`);
    shopColl
      .doc(shopId)
      .delete()
      .catch((error) => {
        console.error("Error removing document: ", error);
      });

    // update data in VueX
    console.log("start deleteSubject in VueX" + index);
    const shops = context.state.shops;
    var index = shops.indexOf(theShop);
    if (index !== -1) {
      shops.splice(index, 1);
      console.log("removed" + index);
    }
    context.commit("setShop", shops);
  },
};
